/*
 *  RFC 4122 version 4 compliant function. Should have a decent balance between speed and randomness.
 */
export function UUID() {
    const rand = Math.random;
    let nbr,
        randStr = '';
    do {
        nbr = rand();
        randStr += nbr.toString(16).substring(3, 9);
    } while (randStr.length < 30);
    return (
        randStr.substring(0, 8) +
        '-' +
        randStr.substring(8, 12) +
        '-4' +
        randStr.substring(12, 15) +
        '-' +
        (((nbr * 4) | 0) + 8).toString(16) + // [89ab]
        randStr.substring(15, 18) +
        '-' +
        randStr.substring(18, 30)
    );
}
