import {ComposablePlotEngineConfig} from "@umetrics/studio-ui-design-system";

export const validateRuleForDeleteScale = (config: ComposablePlotEngineConfig, scaleName: string) => !config.main.some(m => m.serie?.some(se => se.scaleX === scaleName || se.scaleY === scaleName) ||
    m.shape?.some(se => se.scaleX === scaleName || se.scaleY === scaleName) ||
    m.axis?.some(se => se.scaleName === scaleName)
);
export const validateRuleForNewData = (config: ComposablePlotEngineConfig, newName: string) => {
    return !config?.main?.some(m =>
        m.serie?.some(s => s.name === newName) ||
        m.shape?.some(s => s.shapeName === newName)
    );
}

export const validateRuleForScaleChange = (config: ComposablePlotEngineConfig, newScaleName: string | undefined, prevScaleName: string | undefined, otherScaleName?: string | undefined) => {
    const newScale = config?.scale.find(s => s.name === newScaleName);
    if (!newScale) {
        // No such new scale
        return false;
    }
    const prevScale = config?.scale.find(s => s.name === prevScaleName);
    const otherScale = config?.scale.find(s => s.name === otherScaleName);

    if (prevScale) {
        // Validate not change orientation, cant be supported
        return newScale.orientation === prevScale.orientation &&
            (!otherScale || newScale.orientation !== otherScale.orientation);
    }
    return true; // Cant validate prevScale or otherScale, assume ok
}