import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import {getSuiStyleBase, getSuiStyleTypography, getSuiStyleVariables} from "@umetrics/studio-ui-design-system";

Promise.all([
    getSuiStyleVariables(),
    getSuiStyleTypography(),
    getSuiStyleBase(),
]).then(() => console.log("Styles loaded"));


const root = ReactDOM.createRoot(
    document.getElementById('root') as HTMLElement
);
root.render(
    <React.StrictMode>
        <App/>
    </React.StrictMode>
);
