import {
    CSSResultArray,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
    style,
    unsafeCSS,
} from '@umetrics/sartorius-ui-base';

export const errorStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const blue = cssVariableAsVar('color_tertiary_5');
    const text = cssVariableAsVar('color_secondary_7');

    return [
        style,
        unsafeCSS(`

        ${root} {
            height: 100%;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            background-color: transparent;
            color: ${text};
        }

        ${child('container')} {
            width: 70%;
            max-width: 600px;
            padding: 20px;
        }

        ${child('header')} {
            font-size: 32px;
            font-weight: 700;
            margin-bottom: 16px;
            display: flex;
            align-items: baseline;
            flex-wrap: wrap;
        }
        
        ${is('small')} ${child('header')} {
            font-size: 24px;
        }
        
        ${child('header')} svg {
            height: 80px;
            vertical-align: bottom;
            margin-bottom: 5px;
        }
        
        ${is('small')} ${child('header')} svg {
            height: 40px;
            margin-bottom: 3px;
        }

        ${child('description')} {
            font-size: 16px;
            font-weight: 400; 
            line-height: 24px;
            letter-spacing: 0px;
            margin-bottom: 16px;
        }
        
        ${is('small')} ${child('description')} {
            font-size: 14px;
            font-weight: 400;
            line-height: 21px;
            letter-spacing: 0px;
            margin-bottom: 16px;
        }

        ${child('link')} {
            color: ${blue};
        }

        ${child('link')}:hover {
            color: ${blue};
        }

        ${child('extra')} {
            margin-top: 16px;
            font-size: 14px;
        }
        
        ${is('small')} ${child('extra')} {
            font-size: 12px;
        }
        
        ${child('icon')} {
            transform: rotate(0deg);
            transition: transform 0.15s linear;
        }

        ${is('openCollapse')} ${child('icon')} {
            transform: rotate(180deg);
        }

        ${child('button')} {
            margin-top: 16px;
        }

        ${child('details-button')} {
            color: ${blue};
            font-size: 14px;
            font-weight: 400;
            line-height: 21px;
            letter-spacing: 0px;
            padding: 0;
            border: none;
            background: none;
            cursor: pointer;
        }

        `),
    ] as CSSResultArray;
};
