import { defineElement, html } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { skeletonStyles } from './skeletonStyle';
import { SkeletonRectangleProperties } from './skeletonRectangleProperties';
import { Skeleton } from './skeleton';

@defineElement({ tagName: 'skeleton-rectangle', styles: skeletonStyles, version })
export class SkeletonRectangle extends Skeleton implements SkeletonRectangleProperties {
    protected renderInner(baseClassName, rootClasses) {
        return html`
            <label class="${rootClasses}">
                <div
                    class="${baseClassName}-background-color"
                    style="width: ${this.width}; height: ${this.height};"
                ></div>
            </label>
        `;
    }
}
