import { CSSResult, unsafeCSS } from '@lit/reactive-element/css-tag.js';

export type Breakpoint =
    | 'mobile' /*Deprecated*/
    | 'tablet' /*Deprecated*/
    | 'desktop' /*Deprecated*/
    | 'xs'
    | 's'
    | 'm'
    | 'l'
    | 'xl';

export const breakpoint_xs_maxval = 575;
export const breakpoint_s_maxval = 767;
export const breakpoint_m_maxval = 1200;
export const breakpoint_l_maxval = 3840;
export const breakpoint_xl_maxval = Infinity;

export const breakpoint_xs = `${breakpoint_xs_maxval}px`;
export const breakpoint_s = `${breakpoint_s_maxval}px`;
export const breakpoint_m = `${breakpoint_m_maxval}px`;
export const breakpoint_l = `${breakpoint_l_maxval}px`;
export const breakpoint_xl = `${breakpoint_xl_maxval}px`;

export const breakpoints = [
    { name: 'mobile', query: breakpoint_s } /*Deprecated*/,
    { name: 'tablet', query: breakpoint_m } /*Deprecated*/,
    { name: 'desktop', query: breakpoint_l } /*Deprecated*/,
    { name: 'xs', query: breakpoint_xs },
    { name: 's', query: breakpoint_s },
    { name: 'm', query: breakpoint_m },
    { name: 'l', query: breakpoint_l },
    { name: 'xl', query: breakpoint_xl },
];

export const doMediaquery = (bp: Breakpoint, content: string | CSSResult): CSSResult => {
    const breakpoint = breakpoints.find((s) => {
        return s.name === bp;
    }) || { query: 'unknown' };

    return unsafeCSS(`@media screen and (max-width:${breakpoint.query}) { ${content} }`);
};

export const getBreakpoint = (bps: number[]): number => {
    /* innerWidth is the shit. clientwidth will be total width with scrollbars */
    const clientWidth = window.innerWidth;
    const breakPointHits = bps.filter((item) => item >= clientWidth);
    return breakPointHits && breakPointHits.length > 0 ? Math.min(...breakPointHits) : Infinity;
};

export const getCurrentBreakpoint = (): Breakpoint => {
    const actual = getBreakpoint([
        breakpoint_xs_maxval,
        breakpoint_s_maxval,
        breakpoint_m_maxval,
        breakpoint_l_maxval,
        breakpoint_xl_maxval,
    ]);
    switch (actual) {
        case breakpoint_xs_maxval:
            return 'xs';
        case breakpoint_s_maxval:
            return 's';
        case breakpoint_m_maxval:
            return 'm';
        case breakpoint_l_maxval:
            return 'l';
        case breakpoint_xl_maxval:
            return 'xl';
        default:
            throw new Error(`Unexpected breakpoint`);
    }
};
