import {
    ComposablePlotEngineConfig,
    getSuiSelectOptionReact,
    getSuiSelectReact
} from "@umetrics/studio-ui-design-system";
import {NewSelectSize} from "@umetrics/sartorius-ui-select";
import "./SelectScale.css";

const Select = getSuiSelectReact();
const SelectOption = getSuiSelectOptionReact();

interface SelectScaleProps {
    value: string | undefined;
    orientation: 'horizontal' | 'vertical';
    onChange: (e: any) => void;
    config: ComposablePlotEngineConfig;
}

const SelectScale: React.FC<SelectScaleProps> = ({value, orientation, onChange, config}) => {
    return (
        <Select className={"SelectScale"} label={orientation === "horizontal" ? "Scale X" : "Scale Y"}
                size={NewSelectSize.SMALL} value={value}
                onNewSelectionChange={(e: any) => onChange(e)}>
            {config.scale.filter(s => s.orientation === orientation).map(s => (
                <SelectOption key={s.name} value={s.name} label={s.name} selected={s.name === value}/>
            ))}
        </Select>
    );
};
export default SelectScale;