import { BaseElement, createClassName, property, booleanConverter } from '@umetrics/sartorius-ui-base';
import { SkeletonProperties } from './skeletonProperties';

export abstract class Skeleton extends BaseElement implements SkeletonProperties {
    // Public members
    @property({ type: String })
    public width = '100%';
    @property({ type: String })
    public height = '100%';
    @property({ type: Boolean, converter: booleanConverter, attribute: 'on-gray' })
    onGray = false;

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), { onGray: this.onGray });
        return this.renderInner(baseClassName, rootClasses);
    }

    protected abstract renderInner(baseClassName, rootClasses);
}
