import { consume, createContext } from '@lit-labs/context';
import { BaseElement, property, type PropertyValueMap, state } from '@umetrics/sartorius-ui-base';
import { Domain2D } from './domain';
import { D3Scale, ScaleMap, ScaleWithMeta, ScaleZoomMap } from './scale';
import { Constructor } from './types';
import { defaultZoomLens } from './constants';
import { dataChangeDetection } from '../plottable/dataChangeDetection';

export type Dimension = [number, number];

export const plotRootElementContext = createContext<HTMLElement>('plot-root-element-context');
export const domainContext = createContext<Domain2D | undefined>('plot-context');
export const viewContext = createContext<Domain2D | undefined>('view-context');
export const scaleZoomMapContext = createContext<ScaleZoomMap | undefined>('scale-zoom-map-context');
export const sizeContext = createContext<Dimension | undefined>('size-context');
export const scaleContext = createContext<ScaleMap>('scale-context2');

export interface WithContextProps {
    scaleX?: string;
    scaleY?: string;
}

export declare abstract class WithContextInterface extends BaseElement implements WithContextProps {
    plotRootElement?: HTMLElement;
    fittedDomain?: Domain2D;
    size?: Dimension;
    viewbox?: Domain2D;
    scaleZoomMap?: ScaleZoomMap;
    scaleMap?: ScaleMap;
    scaleX?: string;
    scaleY?: string;

    getScales(): [D3Scale, D3Scale];

    getScalesWithMeta(): [ScaleWithMeta, ScaleWithMeta];
}

const SCALE_X_DEFAULT = 'x';
const SCALE_Y_DEFAULT = 'y';

export const WithContext = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class PlottableElement extends superClass {
        /**
         * Plot root element that hosts composable tree
         */
        @consume({ context: plotRootElementContext, subscribe: true })
        @property({ attribute: false })
        plotRootElement?: HTMLElement;
        @consume({ context: domainContext, subscribe: true })
        @property({ attribute: false, hasChanged: dataChangeDetection })
        fittedDomain?: Domain2D;
        @consume({ context: sizeContext, subscribe: true })
        @property({
            attribute: false,
            hasChanged: dataChangeDetection,
        })
        size?: Dimension;

        @state({ hasChanged: dataChangeDetection })
        viewbox?: Domain2D;

        @consume({ context: scaleZoomMapContext, subscribe: true })
        @property({ attribute: false, hasChanged: dataChangeDetection })
        scaleZoomMap?: ScaleZoomMap;

        @consume({ context: scaleContext, subscribe: true })
        @property({ attribute: false })
        // It should have "hasChanged: dataChangeDetection", though there are some problem with the update logic!?
        // For example axis with bandscale will not update correctly if this scaleMap does not trigger another update (new object but with same values)
        scaleMap?: ScaleMap;

        @property({ type: String, reflect: true, attribute: 'scale-x' })
        set scaleX(val: string) {
            this._scaleX = val ?? SCALE_X_DEFAULT;
        }

        get scaleX() {
            return this._scaleX;
        }

        @property({ type: String, reflect: true, attribute: 'scale-y' })
        set scaleY(val: string) {
            this._scaleY = val ?? SCALE_Y_DEFAULT;
        }

        get scaleY() {
            return this._scaleY;
        }

        private _scaleX = SCALE_X_DEFAULT;
        private _scaleY = SCALE_Y_DEFAULT;

        getScales(): [D3Scale, D3Scale] | null {
            const scales = this.getScalesWithMeta();
            if (!scales) return null;
            return [scales[0].scale, scales[1].scale];
        }

        getScalesWithMeta(): [ScaleWithMeta, ScaleWithMeta] | null {
            if (!this.scaleMap?.[this.scaleX] || !this.scaleMap?.[this.scaleY]) return null;
            return [this.scaleMap[this.scaleX], this.scaleMap[this.scaleY]];
        }

        protected willUpdate(_changedProperties: PropertyValueMap<this>): void {
            super.willUpdate(_changedProperties);

            if (_changedProperties.has('scaleZoomMap')) {
                this.viewbox = {
                    x: this.scaleZoomMap?.[this.scaleX] ?? defaultZoomLens,
                    y: this.scaleZoomMap?.[this.scaleY] ?? defaultZoomLens,
                };
            }
        }
    }

    return PlottableElement as Constructor<WithContextInterface> & T;
};
