import React, {ReactNode, useEffect} from "react";

import './DragDrop.css';
import {getSuiButtonReact, getSuiDropdownReact, getSuiIconReact,} from "@umetrics/studio-ui-design-system";

const Dropdown = getSuiDropdownReact();
const Button = getSuiButtonReact();
const Icon = getSuiIconReact();

export interface ClickerProps {
    clicker: { id: string, x: number, y: number } | null;
    setClicker: (c: { id: string, x: number, y: number } | null) => void;
    onClose?: () => void;
}

let lastCloseTime = 0; // Global timestamp

const Clicker: React.FC<ClickerProps & {
    canDelete: boolean;
    onDelete: () => void;
    children: ReactNode;
}> = ({
          clicker,
          setClicker,
          children,
          canDelete,
          onDelete,
          onClose
      }) => {

    useEffect(() => {
        if (clicker && Date.now() - lastCloseTime < 100) {
            console.log("Blocked reopening due to quick re-click.");
            setClicker(null);
        }
    }, [clicker]); // Run this effect when `clicker` changes

    return (clicker && (
        <>
            <div id="clicker"
                 style={{position: "fixed", left: clicker.x, top: clicker.y}}></div>
            <Dropdown open objectId={"clicker"} clickOutsideClose
                      onClose={() => {
                          lastCloseTime = Date.now();
                          if (onClose) {
                              onClose();
                          }
                          setClicker(null);
                          console.log("close");
                      }}>
                <div style={{
                    padding: "0 8px 0 8px", display: "flex",
                    flexDirection: "column",
                    gap: "8px"
                }}>
                    <div style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "end",
                        gap: "8px",
                    }}>
                        {canDelete && <Button compact buttonSize={"extra-small"}
                                              type={"secondary"}
                                              onClick={() => {
                                                  onDelete();
                                                  setClicker(null);
                                                  if (onClose) {
                                                      onClose();
                                                  }
                                              }}><Icon
                            slot={"icon-slot"} icon={"deleteAction"}></Icon>
                        </Button>}

                        <Button compact buttonSize={"extra-small"}
                                type={"secondary"}
                                onClick={() => {
                                    setClicker(null);
                                    if (onClose) {
                                        onClose();
                                    }
                                }}><Icon
                            slot={"icon-slot"} icon={"cancel"}></Icon>
                        </Button>
                    </div>
                    {children}
                </div>
            </Dropdown></>
    ));
};
export default Clicker;