import { BaseInputProperties } from '@umetrics/sartorius-ui-base-input';
import type { DropdownPlacement } from '@umetrics/sartorius-ui-dropdown';

export enum NewSelectVariant {
    Default = 'default',
    ContainerHeader = 'container-header',
}
export enum NewSelectSize {
    SMALL = 'small',
    MEDIUM = 'medium',
    LARGE = 'large',
    XLARGE = 'xlarge',
}
export enum NewSelectSlot {
    ROOT_ICON = 'root-icon',
    SUFFIX_ICON = 'suffix-icon',
    LABEL_ICON = 'label-icon',
}

export interface SelectOption {
    label: string;
    value?: string;
    icon?: string;
    clickCallback?: (e: Event) => void;
    /* Only flat top level options support, no array option:SelectOption[] here that expand to a group! */
}
export interface SelectOptions {
    scrolledItem?: number /* [0..n] or -1/undef if no scroll to */;
    option: SelectOption[] /* top level options */;
}

export interface AutoSelect {
    matchingKeyWords: string[];
    threshold?: number;
}

export interface NewSelectProperties extends BaseInputProperties<string | string[]> {
    placeholder?: string;
    fullWidth?: boolean;
    colorLine?: string;
    variant?: NewSelectVariant;
    required?: boolean;
    value?: string | string[];
    open?: boolean;
    forcePlacement?: DropdownPlacement | string;
    multiple?: boolean;
    searchable?: boolean;
    clearButton?: boolean;
    size?: NewSelectSize;
    readOnly?: boolean;
    extension?: boolean;
    autoSelect?: AutoSelect;
    options?: SelectOptions /* Dynamic and virtualized Select options. Use when huge amount of options! */;

    /* Emitted events, for react users */
    onNewSelectionChange?: any;
    onToggle?: (
        e: CustomEvent<{
            open: boolean;
            value: string;
        }>
    ) => void;
}
