import * as React from 'react';

export type ReactifyWebComponent<T> = React.ForwardRefExoticComponent<
    T &
        React.PropsWithChildren<T> &
        React.PropsWithoutRef<T> &
        React.RefAttributes<HTMLElement> &
        React.HTMLAttributes<HTMLElement>
>;
type ElementProps<I> = I & Partial<Omit<I, keyof HTMLElement>>;
type ComponentProps<I> = React.HTMLAttributes<I> & ElementProps<I>;

const reservedReactProperties = new Set(['children', 'localName', 'ref', 'style', 'className']);
const listenedEvents = new WeakMap<Element, Map<string, EventListenerObject>>();

let studio = window['studio'];
if (!studio) {
    studio = window['studio'] = {};
}
let sui = studio['sui'];
if (!sui) {
    sui = studio['sui'] = {};
}
sui.listenedEvents = () => listenedEvents;

/**
 * convert react synthetic prop to kebab-case event
 *
 * @param propName - react synthetic event like onMyEventName. Will be remapped to my-event-name
 * @private
 */
const _convertPropToEventname = (propName: string) => {
    let eventName = propName;
    if (eventName.match(/^on[A-Z]/gm)) {
        const toKebabCase = (value: string): string => {
            const chars: string[] = [];

            for (let i = 0; i < value.length; i++) {
                const char = value[i];
                const charLowerCase = char.toLowerCase();

                if (char !== charLowerCase && i > 0) {
                    chars.push('-');
                }

                chars.push(charLowerCase);
            }

            return chars.join('');
        };
        const name = eventName.substring(2);
        if (
            ['MouseEnter', 'MouseLeave', 'MouseDown', 'MouseUp', 'DragStart', 'DragEnd', 'DragOver', 'DragLeave'].some(
                (x) => x === name
            )
        ) {
            eventName = name.toLowerCase();
        } else {
            eventName = toKebabCase(name);
        }
    }

    return eventName;
};

/**
 * Adds an event listener for the specified event to the given node. In the
 * React setup, there should only ever be one event listener. Thus, for
 * efficiency only one listener is added and the handler for that listener is
 * updated to point to the given listener function.
 */
const addOrUpdateEventListener = (node: Element, event: string, listener: (event?: Event) => void) => {
    let events = listenedEvents.get(node);
    if (events === undefined) {
        events = new Map();
        listenedEvents.set(node, events);
    }
    let handler = events.get(event);
    if (listener !== undefined) {
        // If necessary, add listener and track handler
        if (handler === undefined) {
            handler = { handleEvent: listener };
            events.set(event, handler);
            node.addEventListener(_convertPropToEventname(event), handler);
            // Otherwise just update the listener with new value
        } else {
            handler.handleEvent = listener;
        }
        // Remove listener if one exists and value is undefined
    } else if (handler !== undefined) {
        events.delete(event);
        node.removeEventListener(_convertPropToEventname(event), handler);
    }
};

/**
 * Sets properties and events on custom elements. These properties and events
 * have been pre-filtered so we know they should apply to the custom element.
 */
const isEventProp = (prop: string, value: any) => {
    return prop && prop.match(/^on[A-Z]/gm) && typeof value === 'function';
};
const setProperty = <E extends HTMLElement>(node: E, name: string, value: unknown, old: unknown) => {
    // Dirty check event value.
    if (value !== old && isEventProp(name, value)) {
        addOrUpdateEventListener(node, name, value as (e?: Event) => void);
        return;
    }
    if (name.indexOf('-') > -1) {
        if (value !== undefined && value !== null && value !== '') {
            node.setAttribute(name, '' + value);
        } else {
            node.removeAttribute(name);
        }
        return;
    }

    // But don't dirty check properties; elements are assumed to do this.
    node[name as keyof E] = value as E[keyof E];

    // This block is to replicate React's behavior for attributes of native
    // elements where `undefined` or `null` values result in attributes being
    // removed.
    // https://github.com/facebook/react/blob/899cb95f52cc83ab5ca1eb1e268c909d3f0961e7/packages/react-dom-bindings/src/client/DOMPropertyOperations.js#L107-L141
    //
    // It's only needed here for native HTMLElement properties that reflect
    // attributes of the same name but don't have that behavior like "id" or
    // "draggable".
    if ((value === undefined || value === null) && name in HTMLElement.prototype) {
        node.removeAttribute(name);
    }
};

/**
 * Creates a React component for a custom element. Properties are distinguished
 * from attributes automatically (*-*), and events (on*) can be configured so they are added
 * to the custom element as event listeners.
 *
 * @param tagName WC tag
 *
 */

export const reactifySartoriusUiComponent = <I>(tagName: string): ReactifyWebComponent<I> => {
    type Props = ComponentProps<I>;
    const ReactComponent = React.forwardRef<I, Props>((props, ref) => {
        const prevElemPropsRef = React.useRef(new Map());
        const elementRef = React.useRef<I | null>(null);

        // Props to be passed to React.createElement
        const reactProps: Record<string, unknown> = {};
        // Props to be set on element with setProperty
        const elementProps: Record<string, unknown> = {};

        for (const [k, v] of Object.entries(props)) {
            if (reservedReactProperties.has(k)) {
                // React does *not* handle `className` for custom elements so
                // coerce it to `class` so it's handled correctly.
                reactProps[k === 'className' ? 'class' : k] = v;
                continue;
            }
            elementProps[k] = v;
        }

        //   run on every re-render.
        React.useLayoutEffect(() => {
            if (elementRef.current === null) {
                return;
            }
            const newElemProps = new Map();
            /*eslint guard-for-in: 0 */
            for (const key in elementProps) {
                setProperty(elementRef.current as HTMLElement, key, props[key], prevElemPropsRef.current.get(key));
                prevElemPropsRef.current.delete(key);
                newElemProps.set(key, props[key]);
            }

            // "Unset" any props from previous render that no longer exist.
            // Setting to `undefined` seems like the correct thing to "unset"
            // but currently React will set it as `null`.
            for (const [key, value] of prevElemPropsRef.current) {
                setProperty(elementRef.current as HTMLElement, key, undefined, value);
            }
            prevElemPropsRef.current = newElemProps;
        });

        return React.createElement(tagName, {
            ...reactProps,
            ref: React.useCallback(
                (node: I) => {
                    elementRef.current = node;
                    if (typeof ref === 'function') {
                        ref(node);
                    } else if (ref !== null) {
                        ref.current = node;
                    }
                },
                [ref]
            ),
        });
    });

    // No set of a displayName!
    return ReactComponent as ReactifyWebComponent<I>;
};
