import React, {useCallback} from "react";
import './DragDrop.css';
import {
    getSuiButtonReact,
    getSuiColorPickerReact,
    getSuiIconReact,
    getSuiRadioGroupReact,
    getSuiRadioReact,
    getSuiSliderReact,
    getSuiTextfieldReact,
    TextFieldSize
} from "@umetrics/studio-ui-design-system";
import {ComposablePlotEngineConfig} from "@umetrics/sartorius-ui-composable-plot-engine";
import Clicker, {ClickerProps} from "./Clicker";
import SelectScale from "./SelectScale";
import {validateRuleForScaleChange} from "./configRules";

const TextField = getSuiTextfieldReact();
const Radio = getSuiRadioReact();
const RadioGroup = getSuiRadioGroupReact();
const Button = getSuiButtonReact();
const Icon = getSuiIconReact();
const Slider = getSuiSliderReact();


const ColorPicker = getSuiColorPickerReact();

interface ClickerSerieProps extends ClickerProps {
    config: ComposablePlotEngineConfig;
    setConfig: (config: ComposablePlotEngineConfig) => void;
}

const ClickerSerie: React.FC<ClickerSerieProps> = ({config, setConfig, clicker, setClicker, onClose}) => {

    const onDelete = useCallback(() => {
        config.main?.forEach(m => {
            const updatedSerie = m.serie?.filter((s: any) => s.name !== clicker?.id);
            if (updatedSerie?.length !== m.serie?.length) {
                m.serie = updatedSerie ? [...updatedSerie] : [];
                setConfig({...config});
            }
        });
    }, [config, setConfig, clicker]);

    const serie = config.main[0].serie?.find(s => s.name === clicker?.id);

    return (clicker && (
        <Clicker
            setClicker={setClicker}
            clicker={clicker}
            canDelete={true}
            onDelete={onDelete}
            onClose={onClose}>

            <div style={{
                display: "flex",
                flexDirection: "row",
                gap: "8px"
            }}>
                <TextField label={"Name"}
                           placeholder={"Name"}
                           size={TextFieldSize.SMALL}
                           value={serie!.name}
                           onChange={(e: any) => {
                               serie!.name = e.target.value;
                               setConfig({...config});
                           }}></TextField>
                <ColorPicker label={"Color"}
                             value={serie?.color as string}
                             size={"small"}
                             onColorChange={e => {
                                 serie!.color = e.detail.value;
                                 setConfig({...config});
                             }}></ColorPicker>
            </div>

            <div style={{
                display: "flex",
                flexDirection: "row",
                border: "1px solid lightgray",
                width: "fit-content",
            }}>
                <Button compact buttonSize={"small"}
                        type={(serie?.type as any) === "line-scatter" ? "secondary" : "tertiary"}
                        onClick={() => {
                            serie!.type = "line-scatter" as any;
                            setConfig({...config});
                        }}><Icon
                    slot={"icon-slot"} icon={"plot"}></Icon> </Button>
                <Button compact buttonSize={"small"}
                        type={(serie?.type as any) === "scatter" ? "secondary" : "tertiary"}
                        onClick={() => {
                            serie!.type = "scatter";
                            setConfig({...config});
                        }}><Icon slot={"icon-slot"}
                                 icon={"plotScatter"}></Icon>
                </Button>
                <Button compact buttonSize={"small"}
                        type={(serie?.type as any) === "line" ? "secondary" : "tertiary"}
                        onClick={() => {
                            serie!.type = "line";
                            setConfig({...config});
                        }}><Icon slot={"icon-slot"}
                                 icon={"plotLine"}></Icon>
                </Button>
                <Button compact buttonSize={"small"}
                        type={(serie?.type as any) === "bar" ? "secondary" : "tertiary"}
                        onClick={() => {
                            serie!.type = "bar";
                            setConfig({...config});
                        }}><Icon slot={"icon-slot"}
                                 icon={"plotBar"}></Icon>
                </Button>
            </div>
            <Slider value={"4"} minValue={1} maxValue={12}></Slider>

            <div style={{display: "flex", flexDirection: "row", gap: "8px"}}>
                <SelectScale value={serie?.scaleX} orientation={"horizontal"}
                             onChange={(e: any) => {
                                 const newScale = e.target.value;
                                 const prevScale = serie!.scaleX;
                                 const otherScale = serie!.scaleY;

                                 if (!validateRuleForScaleChange(config, newScale, prevScale, otherScale)) {
                                     e.target.additionalFeedbackType = "failure";
                                     e.target.additionalFeedbackMessage = "Invalid scale change";
                                     return;
                                 }
                                 e.target.additionalFeedbackType = "";
                                 e.target.additionalFeedbackMessage = "";
                                 serie!.scaleX = newScale;
                                 setConfig({...config});
                             }} config={config}></SelectScale>
                <SelectScale value={serie?.scaleY} orientation={"vertical"}
                             onChange={(e: any) => {
                                 const newScale = e.target.value;
                                 const prevScale = serie!.scaleY;
                                 const otherScale = serie!.scaleX;

                                 if (!validateRuleForScaleChange(config, newScale, prevScale, otherScale)) {
                                     e.target.additionalFeedbackType = "failure";
                                     e.target.additionalFeedbackMessage = "Invalid scale change";
                                     return;
                                 }
                                 e.target.additionalFeedbackType = "";
                                 e.target.additionalFeedbackMessage = "";
                                 serie!.scaleY = newScale;
                                 setConfig({...config});
                             }} config={config}></SelectScale>
            </div>

        </Clicker>
    ));
};
export default ClickerSerie;