import { CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, style, css } from '@umetrics/sartorius-ui-base';

export const skeletonStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const grayBefore = cssVariableAsVar('color_secondary_1');
    const grayIconBefore = cssVariableAsVar('color_secondary_3');
    const grayAfter = cssVariableAsVar('color_secondary_2');
    const grayIconAfter = cssVariableAsVar('color_secondary_4');

    return [
        style,
        css`
            :host {
                outline: 0;
                width: 100%;
                height: 100%;
            }

            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            ${root} {
                display: flex;
                flex-direction: column;
                margin: 0;
                width: 100%;
                height: 100%;
            }

            ${child('background-color')} {
                background: linear-gradient(to right, #00000008 40%, #ffffff1a 50%, #00000008 60%);
                background-size: 250%;
                background-position-x: -100%;
                animation: shimmer 4s infinite linear;
            }
            ${is('onGray')} ${child('background-color')} {
                background: linear-gradient(to right, #ffffff4d 40%, #ffffffe6 50%, #ffffff4d 60%);
                background-size: 250%;
                background-position-x: -100%;
                animation: shimmer 4s infinite linear;
            }

            ${child('text-background')} {
                background: linear-gradient(to bottom, #fff, #fff 50%, ${grayAfter}, ${grayAfter} 50%);
                background-size: 100% 20px;
                animation: color 2s infinite;
            }

            ${child('image-background')} {
                display: flex;
                justify-content: center;
                align-items: center;
                color: ${grayIconBefore};
                animation: color 2s infinite;
            }

            ${child('background-text')} {
                background-color: ${grayBefore};
                height: 12px;
                min-height: 12px;
                margin-bottom: 8px;
            }

            @keyframes shimmer {
                0% {
                    background-position-x: 100%;
                }
                50% {
                    background-position-x: 0%;
                }
                100% {
                    background-position-x: 0%;
                }
            }

            @keyframes color {
                0% {
                    background-color: ${grayBefore};
                    color: ${grayIconBefore};
                }
                50% {
                    background-color: ${grayAfter};
                    color: ${grayIconAfter};
                }
                100% {
                    background-color: ${grayBefore};
                    color: ${grayIconBefore};
                }
            }
        `,
    ] as CSSResultArray;
};
