import { consume, createContext, provide } from '@lit-labs/context';
import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    nothing,
    property,
    type PropertyValueMap,
    type PropertyValues,
} from '@umetrics/sartorius-ui-base';
import { Constructor } from '../data/types';
import { version } from '../metadata';
import { namespacedTag } from '../tag';
import { interactionStyles } from './interactionStyles';

export const interactionContext = createContext<HTMLDivElement | undefined>('interaction-context');
export const interactionContextSlot = createContext<string | undefined>('interaction-context-slot');
export const mouseEventContext = createContext<MouseEvent | undefined>('mouse-event-context');

export interface InteractionContextProperties {}

export declare abstract class InteractionContextInterface extends BaseElement {
    interactionTarget?: HTMLDivElement;
    interactionTargetSlot?: string;
}

@defineElement({ tagName: namespacedTag('interaction'), styles: interactionStyles, version })
export class InteractionContext extends BaseElement implements InteractionContextProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @provide({ context: interactionContext })
    interactionTarget: HTMLDivElement | undefined = undefined;
    @provide({ context: interactionContextSlot })
    interactionTargetSlot: string | undefined = undefined;

    @consume({ context: mouseEventContext, subscribe: true })
    @property({ attribute: false })
    _mouseEvent?: MouseEvent;

    private _interactionTarget;
    private _outerEnter?: boolean;
    private _innerEnter?: boolean;

    public outerEvent(e: MouseEvent) {
        if (e.type === 'mouseenter') {
            this._outerEnter = true;
            this._mouseEnter();
        } else if (e.type === 'mouseleave') {
            this._outerEnter = false;
            this._mouseLeave();
        }
        return true;
    }

    /**
     * Interaction does not export anything. This override avoid warning generated from base mixin Plottable.
     */
    public exportSVG = () => nothing;

    protected update(_changedProperties: PropertyValues<this>): void {
        super.update(_changedProperties);

        if (_changedProperties.has('_mouseEvent') && this._mouseEvent) {
            const target = this._mouseEvent.target as HTMLElement;
            if ((!this.slot && target?.id === 'main-area') || this.slot === target?.id) {
                if (this.outerEvent(this._mouseEvent)) {
                    // Fix this with some subscribe or context pattern.
                    this.shadowRoot!.querySelector('slot')!
                        .assignedElements()
                        .forEach((p) => {
                            if ((p as any).outerEvent && this._mouseEvent) {
                                (p as any).outerEvent(this._mouseEvent);
                            }
                        });
                }
            }
        }
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        return html`
            <div
                id="interaction-target"
                class="${rootClasses}"
                @mouseenter=${() => {
                    this._innerEnter = true;
                    this._mouseEnter();
                }}
                @mouseleave=${() => {
                    this._innerEnter = false;
                    this._mouseLeave();
                }}
            >
                <slot></slot>
            </div>
        `;
    }

    protected firstUpdated(_changedProperties: PropertyValueMap<any> | Map<PropertyKey, unknown>): void {
        this._interactionTarget = this.shadowRoot!.querySelector('#interaction-target') as HTMLDivElement;
        this.interactionTarget = this._interactionTarget;
        this.interactionTargetSlot = this.slot;
        super.firstUpdated(_changedProperties);
    }

    private _mouseEnter = () => {
        if (this._innerEnter || this._outerEnter) {
            this._interactionTarget?.setAttribute('hover', '');
        }
    };

    private _mouseLeave = () => {
        if (!this._innerEnter && !this._outerEnter) {
            this._interactionTarget?.removeAttribute('hover');
        }
    };
}

export const WithInteractionContext = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class InteractionContextElement extends superClass {
        @consume({ context: interactionContext, subscribe: true })
        @property({ attribute: false })
        interactionTarget?: HTMLDivElement;
        @consume({ context: interactionContextSlot, subscribe: true })
        @property({ attribute: false })
        interactionTargetSlot?: string;
    }

    return InteractionContextElement as Constructor<InteractionContextInterface> & T;
};
