import { BaseInputProperties } from '@umetrics/sartorius-ui-base-input';

export enum TextFieldSlot {
    LABEL_ICON_SLOT = 'label-icon-slot',
    INPUT_BUTTON_SLOT = 'input-button-slot',
    SUFFIX_ICON_SLOT = 'suffix-icon-slot',
}

export type TextFieldType = 'text' | 'number';
export enum TextFieldSize {
    SMALL = 'small',
    MEDIUM = 'medium',
    LARGE = 'large',
    XLARGE = 'xlarge',
}

export interface TextFieldProperties extends BaseInputProperties {
    placeholder?: string;
    autofocus?: boolean;
    required?: boolean;
    regexp?: string;
    maxLength?: number;
    validInterval?: string;
    fullWidth?: boolean;
    unit?: string;
    type?: TextFieldType | string;
    size?: TextFieldSize;
    onInput?: any;
    readOnly?: boolean;
}
