import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const shapeDrawingContextStyles: GenerateStyles = (tagName) => {
    const { root, child } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
                transition:
                    fill 0.4s,
                    opacity 0.4s,
                    r 0.4s;
            }

            :host {
                display: block;
                width: 100%;
                height: 100%;
            }

            ${root} {
                width: 100%;
                height: 100%;
                overflow: hidden;
            }

            svg {
                display: block;
                width: 100%;
                height: 100%;
            }

            ${child('target')} {
                fill: var(--shape-fill-color, none);
                fill-opacity: var(--shape-fill-opacity, 1.0);
                stroke: var(--shape-stroke-color, black);
                stroke-width: var(--shape-stroke-width, 2);
                stroke-dasharray: var(--shape-stroke-dasharray, none);
                stroke-opacity: var(--shape-stroke-opacity, 1.0);
            }

            ${child('target')}.highlight {
                fill: var(--shape-highlight-fill-color, none);
                fill-opacity: var(--shape-highlight-fill-opacity, 1.0);
                stroke: var(--shape-highlight-stroke-color, blue);
                stroke-width: var(--shape-highlight-stroke-width, 4);
                stroke-dasharray: var(--shape-highlight-stroke-dasharray, none);
                stroke-opacity: var(--shape-highlight-stroke-opacity, 1.0);
            }

            ${child('hover-target')} {
                position: relative;
                pointer-events: painted;
                cursor: var(--shape-cursor, move);
                fill: transparent;
                stroke: transparent;
                stroke-width: var(--shape-hover-stroke-width, 10);
                stroke-linecap: var(--shape-hover-stroke-linecap, square);
                stroke-dasharray: var(--shape-hover-stroke-dasharray, none);
            }
            ${child('hover-target')}:hover {
                opacity: initial;
                stroke: var(--shape-hover-stroke, black);
                stroke-opacity: var(--shape-hover-stroke-opacity, 0.1);
            }

            ${child('resize-target')} {
                position: relative;
                pointer-events: painted;
                fill: transparent;
                stroke: transparent;
                stroke-width: var(--shape-resize-stroke-width, 1);
                cursor: var(--shape-cursor, pointer);
            }
            ${child('resize-target')}:hover {
                opacity: initial;
                stroke: var(--shape-resize-stroke, green);
            }
        `,
    ];
};
