import React, {useCallback} from "react";
import './DragDrop.css';
import {
    getSuiButtonReact,
    getSuiColorPickerReact,
    getSuiIconReact,
    getSuiRadioGroupReact,
    getSuiRadioReact,
    getSuiSelectOptionReact,
    getSuiSelectReact,
    getSuiSliderReact,
    getSuiTextfieldReact,
    TextFieldSize
} from "@umetrics/studio-ui-design-system";
import {ComposablePlotEngineConfig} from "@umetrics/sartorius-ui-composable-plot-engine";
import Clicker, {ClickerProps} from "./Clicker";
import {renameKey} from "./utils";
import {validateRuleForNewData, validateRuleForScaleChange} from "./configRules";
import SelectScale from "./SelectScale";

const TextField = getSuiTextfieldReact();
const Radio = getSuiRadioReact();
const RadioGroup = getSuiRadioGroupReact();
const Button = getSuiButtonReact();
const Icon = getSuiIconReact();
const Slider = getSuiSliderReact();
const Select = getSuiSelectReact();
const SelectOption = getSuiSelectOptionReact();

const ColorPicker = getSuiColorPickerReact();

interface ClickerShapeProps extends ClickerProps {
    config: ComposablePlotEngineConfig;
    setConfig: (config: ComposablePlotEngineConfig) => void;
}

const ClickerShape: React.FC<ClickerShapeProps> = ({config, setConfig, clicker, setClicker, onClose}) => {

    if (!config.main[0].shape) {
        config.main[0].shape = [];
    }

    const shape = config.main[0]!.shape?.find((s: any) => s.shapeName === clicker?.id);


    const data = shape && shape.data ? config.data![shape.data] : [];

    const onDelete = useCallback(() => {
        const updatedShapes = config.main[0]!.shape?.filter((s: any) => s.shapeName !== clicker?.id);
        const dataRef = updatedShapes!.some(s => s.data === shape?.data);
        if (!dataRef && shape?.data && config.data) {
            const {[shape.data]: _, ...newObj} = config.data;
            config.data = newObj;
        }
        config.main[0]!.shape = updatedShapes ? [...updatedShapes] : [];
        setConfig({...config});
    }, [config, setConfig, clicker, shape]);

    return (clicker && (
        <Clicker
            setClicker={setClicker}
            clicker={clicker}
            canDelete={true}
            onDelete={onDelete}
            onClose={onClose}>

            <div style={{
                display: "flex",
                flexDirection: "row",
                gap: "8px"
            }}>
                <TextField label={"Name"}
                           placeholder={"Name"}
                           size={TextFieldSize.SMALL}
                           value={shape!.shapeName}
                           onChange={(e: any) => {
                               const oldname = shape?.shapeName!;
                               const newname = e.target.value;
                               if (!validateRuleForNewData(config, newname)) {
                                   e.target.additionalFeedbackType = "failure";
                                   e.target.additionalFeedbackMessage = "Name in use";
                                   return;
                               }
                               e.target.additionalFeedbackType = "";
                               e.target.additionalFeedbackMessage = "";
                               shape!.shapeName = newname;
                               shape!.data = newname;
                               config.data = renameKey(config.data!, oldname, newname);
                               setConfig({...config});
                               setClicker({...clicker, ...{id: newname}});
                           }}></TextField>
            </div>
            <div style={{display: "flex", flexDirection: "row", gap: "8px"}}>
                <ColorPicker label={"Fill"}
                             value={shape?.fillColor as string}
                             size={"small"}
                             onColorChange={e => {
                                 shape!.fillColor = e.detail.value;
                                 setConfig({...config});
                             }}></ColorPicker>
                <ColorPicker label={"Stroke"}
                             value={shape?.strokeColor as string}
                             size={"small"}
                             onColorChange={e => {
                                 shape!.strokeColor = e.detail.value;
                                 setConfig({...config});
                             }}></ColorPicker>
            </div>

            <div style={{
                display: "flex",
                flexDirection: "row",
                border: "1px solid lightgray",
                width: "fit-content",
            }}>
                <Button compact buttonSize={"small"}
                        type={!(shape?.strokeDashArray as any) ? "secondary" : "tertiary"}
                        onClick={() => {
                            shape!.strokeDashArray = "";
                            setConfig({...config});
                        }}><Icon
                    slot={"icon-slot"} icon={"lineSolid"}></Icon> </Button>
                <Button compact buttonSize={"small"}
                        type={(shape?.strokeDashArray as any) === "3 3" ? "secondary" : "tertiary"}
                        onClick={() => {
                            shape!.strokeDashArray = "3 3";
                            setConfig({...config});
                        }}><Icon slot={"icon-slot"}
                                 icon={"lineDashed"}></Icon>
                </Button>
            </div>

            <Slider value={"4"} minValue={1} maxValue={12}></Slider>

            <div style={{display: "flex", flexDirection: "row", gap: "8px"}}>
                <TextField label={"x0"}
                           placeholder={"x0"}
                           size={TextFieldSize.SMALL}
                           value={(data[0] as any).x}
                           onChange={(e: any) => {
                               (data[0] as any).x = e.target.value;
                               setConfig({...config});
                           }}></TextField>
                <TextField label={"y0"}
                           placeholder={"y0"}
                           size={TextFieldSize.SMALL}
                           value={(data[0] as any).y}
                           onChange={(e: any) => {
                               (data[0] as any).y = e.target.value;
                               setConfig({...config});
                           }}></TextField>
            </div>
            <div style={{display: "flex", flexDirection: "row", gap: "8px"}}>
                <TextField label={"x1"}
                           placeholder={"x1"}
                           size={TextFieldSize.SMALL}
                           value={(data[1] as any).x}
                           onChange={(e: any) => {
                               (data[1] as any).x = e.target.value;
                               setConfig({...config});
                           }}></TextField>
                <TextField label={"y1"}
                           placeholder={"y1"}
                           size={TextFieldSize.SMALL}
                           value={(data[1] as any).y}
                           onChange={(e: any) => {
                               (data[1] as any).y = e.target.value;
                               setConfig({...config});
                           }}></TextField>
            </div>

            <div style={{display: "flex", flexDirection: "row", gap: "8px"}}>
                <SelectScale value={shape?.scaleX} orientation={"horizontal"}
                             onChange={(e: any) => {
                                 const newScale = e.target.value;
                                 const prevScale = shape!.scaleX;
                                 const otherScale = shape!.scaleY;

                                 if (!validateRuleForScaleChange(config, newScale, prevScale, otherScale)) {
                                     e.target.additionalFeedbackType = "failure";
                                     e.target.additionalFeedbackMessage = "Invalid scale change";
                                     return;
                                 }
                                 e.target.additionalFeedbackType = "";
                                 e.target.additionalFeedbackMessage = "";
                                 shape!.scaleX = newScale;
                                 setConfig({...config});
                             }} config={config}></SelectScale>
                <SelectScale value={shape?.scaleY} orientation={"vertical"}
                             onChange={(e: any) => {
                                 const newScale = e.target.value;
                                 const prevScale = shape!.scaleY;
                                 const otherScale = shape!.scaleX;

                                 if (!validateRuleForScaleChange(config, newScale, prevScale, otherScale)) {
                                     e.target.additionalFeedbackType = "failure";
                                     e.target.additionalFeedbackMessage = "Invalid scale change";
                                     return;
                                 }
                                 e.target.additionalFeedbackType = "";
                                 e.target.additionalFeedbackMessage = "";
                                 shape!.scaleY = newScale;
                                 setConfig({...config});
                             }} config={config}></SelectScale>
            </div>
        </Clicker>
    ));
};
export default ClickerShape;