import {getSuiComposableAxisReact} from "@umetrics/studio-ui-design-system";
import './DragDropAxis.css';
import {useState} from "react";
import {ComposablePlotEngineConfigAxis} from "@umetrics/sartorius-ui-composable-plot-engine";

interface DragDropAxisProps {
    axis: ComposablePlotEngineConfigAxis;
    slot: string;
    orientation: "horizontal" | "vertical";
    flip?: boolean;
    setClicker: (c: { id: string, x: number, y: number } | null) => void;
    onDrop: (e: React.DragEvent<HTMLElement>, key: string, draggedKey: string, slot: string) => void;
}

const Axis = getSuiComposableAxisReact();


const DragDropAxis: React.FC<DragDropAxisProps> = ({axis, slot, orientation, flip, setClicker, onDrop}) => {
    const [isDraggingOver, setIsDraggingOver] = useState(false);
    const [isDragging, setIsDragging] = useState(false);
    const [isSelected, setIsSelected] = useState(false);

    const axisKey = axis.label!;

    const handleDragOver = (event: React.DragEvent<HTMLElement>) => {
        const draggedKey = event.dataTransfer.getData("text/plain");

        if (draggedKey === axisKey) {
            return;
        }
        event.preventDefault();
        setIsDraggingOver(true);
    };

    const handleDragLeave = () => {
        setIsDraggingOver(false);
    };

    const handleDrop = (event: React.DragEvent<HTMLElement>) => {
        const draggedKey = event.dataTransfer.getData("text/plain");
        if (draggedKey === axisKey) {
            return;
        }
        event.preventDefault();
        setIsDraggingOver(false);
        console.log("Dropped on axis!", axisKey, slot);
        onDrop(event, axisKey, draggedKey, slot);
    };

    return (
        <Axis
            className={`DragDropAxis ${isDraggingOver ? "drag-over" : ""} ${isDragging ? "drag" : ""} ${isSelected ? 'selected' : ""}`}
            key={axisKey}
            label={axis.label}
            slot={slot}
            orientation={orientation}
            axisColor={axis.axisColor}
            labelColor={axis.labelColor}
            tickColor={axis.tickColor || axis.axisColor}
            {...(orientation === "horizontal" ? {scaleX: axis.scaleName} : {scaleY: axis.scaleName})}
            flip={flip}
            draggable
            onDragStart={(e) => {
                console.log(`Dragging axis ${axisKey}`);
                e.dataTransfer.setData("text/plain", "axis:" + axisKey);
                setIsDragging(true);
            }}
            onDragEnd={() => {
                setIsDragging(false);
                setIsDraggingOver(false);
            }}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}

            tabIndex={0} // Could also you click to select and document.click to deselect
            onFocus={() => setIsSelected(true)}
            onBlur={() => setIsSelected(false)}

            onClick={e => {
                setClicker({id: axisKey, x: e.clientX, y: e.clientY});
            }}
        />
    );
};
export default DragDropAxis;