import { TemplateResult } from '@umetrics/sartorius-ui-base';
import { Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { findDataPosition } from '../../plottable/svg/lineUtils';
import { ScaleWithMeta } from '../../data';
import { ShapeProperties } from './shapeDrawingContext';
export type Position = 'left' | 'right' | 'bottom' | 'top' | 'center';
export type VerticalPosition = Exclude<Position, 'left' | 'right'>;
export type HorizontalPosition = Exclude<Position, 'top' | 'bottom'>;
export interface Size {
    width: number;
    height: number;
}

export interface Hint {
    // TemplateResult object, html or svg
    element: TemplateResult;
    // first value allows to position hint from one of the shape sides
    // second value allows to align hint
    position: [HorizontalPosition, VerticalPosition];
    width: number;
    height: number;
}

export enum DragMode {
    MoveLine = 'MoveLine',
    ResizeEnd1 = 'ResizeEnd1',
    ResizeEnd2 = 'ResizeEnd2',
}

export type DataPoints = [Point2D<number | Date | undefined>, Point2D<number | Date | undefined>];

export const isShape = (points: DataPoints) =>
    points[0].x !== undefined &&
    points[0].y !== undefined &&
    points[1].x !== undefined &&
    points[1].y !== undefined &&
    (points[0].x !== points[1].x || points[0].y !== points[1].y);

export const getNewPointsOnDrag = (
    oldPoints: DataPoints,
    deltaX: number,
    deltaY: number,
    dragMode: DragMode | undefined
) => {
    switch (dragMode) {
        case DragMode.ResizeEnd1:
            return [
                {
                    x: Number(oldPoints[0].x) + deltaX,
                    y: Number(oldPoints[0].y) + deltaY,
                },
                oldPoints[1],
            ];
        case DragMode.ResizeEnd2:
            return [
                oldPoints[0],
                {
                    x: Number(oldPoints[1].x) + deltaX,
                    y: Number(oldPoints[1].y) + deltaY,
                },
            ];
        case DragMode.MoveLine:
            return [
                {
                    x: Number(oldPoints[0].x) + deltaX,
                    y: Number(oldPoints[0].y) + deltaY,
                },
                {
                    x: Number(oldPoints[1].x) + deltaX,
                    y: Number(oldPoints[1].y) + deltaY,
                },
            ];
        default:
            throw Error(`${dragMode} doesn't exist in DragMode enum`);
    }
};

export function dispatchShapeDoubleClick(
    element: HTMLElement,
    scales: [ScaleWithMeta, ScaleWithMeta],
    evt: MouseEvent,
    shape: ShapeProperties
) {
    element.dispatchEvent(
        new CustomEvent('shape-double-click', {
            bubbles: true,
            detail: {
                evt,
                point: findDataPosition(element, scales, evt),
                target: shape,
            },
        })
    );
}

export function dispatchShapeCreate(element: HTMLElement, newShapes: ShapeProperties[], shape: ShapeProperties) {
    element.dispatchEvent(
        new CustomEvent('shape-create', {
            bubbles: true,
            detail: {
                evt: new Event('Shape Create'),
                newShapes,
                target: shape,
            },
        })
    );
}

