export function getVerticalShift(verticalPosition: VerticalPosition, height: number) {
    switch (verticalPosition) {
        case 'top': {
            return -height;
        }
        case 'bottom': {
            return 0;
        }
        case 'center':
        default: {
            return -height / 2;
        }
    }
}

export function getHintTranslatePoint(
    points: DataPoints,
    scaledPoints: DataPoints,
    position: Hint['position'],
    width: number,
    height: number
) {
    if (
        points[0].x === undefined ||
        points[1].x === undefined ||
        scaledPoints[0].x === undefined ||
        scaledPoints[1].x === undefined ||
        scaledPoints[0].y === undefined ||
        scaledPoints[1].y === undefined
    ) {
        return { xValue: undefined, yValue: undefined };
    }

    const isFirstValueSmaller = points[0].x < points[1].x;
    const horizontalPosition = position[0];
    const verticalPosition = position[1];
    const verticalShift = getVerticalShift(verticalPosition, height);

    switch (horizontalPosition) {
        case 'left': {
            return {
                xValue: (isFirstValueSmaller ? +scaledPoints[0].x : +scaledPoints[1].x) - width / 2,
                yValue: (isFirstValueSmaller ? +scaledPoints[0].y : +scaledPoints[1].y) + verticalShift,
            };
        }
        case 'right': {
            return {
                xValue: (isFirstValueSmaller ? +scaledPoints[1].x : +scaledPoints[0].x) - width / 2,
                yValue: (isFirstValueSmaller ? +scaledPoints[1].y : +scaledPoints[0].y) + verticalShift,
            };
        }
        case 'center':
        default: {
            return {
                xValue: (+scaledPoints[0].x + +scaledPoints[1].x - width) / 2,
                yValue: (+scaledPoints[0].y + +scaledPoints[1].y) / 2 + verticalShift,
            };
        }
    }
}
