import React from "react";
import './DragDrop.css';
import {getSuiPaneReact} from "@umetrics/studio-ui-design-system";
import Settings from "./Settings";
import type {ComposablePlotEngineConfig} from "@umetrics/sartorius-ui-composable-plot-engine";

const Pane = getSuiPaneReact();

interface AppRightProps {
    config: ComposablePlotEngineConfig;
    setConfig: (config: ComposablePlotEngineConfig) => void
    isMainSelected: boolean,
    isElemSelected: string,

}

const AppRight: React.FC<AppRightProps> = ({config, setConfig, isMainSelected, isElemSelected}) => {


    return (
        <Pane active slot={"right-slot"} position="right">
            <div slot={"header-slot"}>Settings</div>
            <Settings selection={isMainSelected} elemSelection={isElemSelected} config={config} setConfig={setConfig}
                      onPlotChange={(v) => {
                          const cfg = config;
                          cfg.heading = v.heading;
                          setConfig({...cfg});
                      }}></Settings>
        </Pane>
    );
};
export default AppRight;