import './Settings.css';
import {
    getSuiAccordionItemReact,
    getSuiAccordionReact,
    getSuiButtonReact,
    getSuiCheckboxReact,
    getSuiIconReact,
    getSuiTextfieldReact,
    TextFieldSize
} from "@umetrics/studio-ui-design-system";
import type {ComposablePlotEngineConfig} from "@umetrics/sartorius-ui-composable-plot-engine";
import React from "react";
import {validateRuleForDeleteScale} from "./configRules";

const TextField = getSuiTextfieldReact();
const Checkbox = getSuiCheckboxReact();
const Accordion = getSuiAccordionReact();
const AccordionItem = getSuiAccordionItemReact();
const Button = getSuiButtonReact();
const Icon = getSuiIconReact();

interface SettingsProps {
    selection: boolean,
    elemSelection: string,
    config: ComposablePlotEngineConfig,
    setConfig: (config: ComposablePlotEngineConfig) => void,
    onPlotChange: (_: ComposablePlotEngineConfig) => (void),
}


const Settings: React.FC<SettingsProps> = ({selection, elemSelection, onPlotChange, config, setConfig}) => {

    return <Accordion>
        <AccordionItem open>
            <span slot={"label-slot"}>Plot</span>
            <div style={{
                margin: 8,
                display: "flex",
                flexDirection: "column",
                gap: "12px",
            }}>
                <TextField placeholder={"Heading"} size={TextFieldSize.SMALL} value={config.heading}
                           onChange={(e: any) => {
                               config.heading = e.detail.value;
                               onPlotChange(config);
                           }}></TextField>
                <Checkbox label={"Legend"} checked={(config as any).legend} onChange={(e: any) => {
                    console.log("TDO");
                }}></Checkbox>
                <Checkbox label={"Scrollbar"} checked={(config as any).scrollbar} onChange={(e: any) => {
                    console.log("TDO");
                }}></Checkbox>
            </div>
        </AccordionItem>
        <AccordionItem open>
            <span slot={"label-slot"}>Scales</span>
            <div style={{
                margin: 8,
                display: "flex",
                flexDirection: "column",
                gap: 2,
            }}>
                {config.scale.map(s => {
                    const unused = validateRuleForDeleteScale(config, s.name);

                    return <div key={s.name} style={{
                        display: "flex",
                        flexDirection: "row",
                        gap: "4px",
                        alignItems: "center",
                    }}><TextField className="Scale" placeholder={"Scale"} size={TextFieldSize.SMALL}
                                  value={s.name}
                                  onChange={(e: any) => {
                                      s.name = e.detail.value;
                                      onPlotChange(config);
                                  }}></TextField>
                        <span>{s.orientation}</span>
                        {s.domain && (<span>{s.domain[0] as string}-{s.domain![1] as string}</span>)}
                        {unused && <Button compact buttonSize={"extra-small"}
                                           type={"secondary"}
                                           onClick={() => {
                                               const cfg = config;
                                               const updatedScale = cfg.scale?.filter((sc: any) => s.name !== sc.name);
                                               cfg.scale = updatedScale ? [...updatedScale] : [];
                                               setConfig({...cfg});
                                           }}><Icon
                            slot={"icon-slot"} icon={"deleteAction"}></Icon>
                        </Button>}
                    </div>
                })}
            </div>
        </AccordionItem>
    </Accordion>;
};
export default Settings;