import { ScaleWithMeta } from '../../data';

function invertBand(scale, size) {
    const domain = scale.domain();
    const step = scale.step();
    const firstVisibleBand = Math.abs(scale.range()[0]) / step;
    return function (v: number) {
        const index = Math.round(v / size / (step / size) + firstVisibleBand) - 1;
        return domain[Math.max(0, Math.min(index, domain.length - 1))];
    };
}

export function findDataPosition(element: HTMLElement, scales: [ScaleWithMeta, ScaleWithMeta], evt: MouseEvent) {
    if (!scales) return null;

    const [scaleX, scaleY] = scales;
    const { left, top, width, height } = element.getBoundingClientRect();
    const offsetX = evt.clientX - left;
    const offsetY = evt.clientY - top;
    const getVal = (scale, dimension, offset) =>
        scale.scaleType === 'band' ? invertBand(scale.scale, dimension)(offset) : scale.scale.invert(offset);
    return { x: getVal(scaleX, width, offsetX), y: getVal(scaleY, height, offsetY) };
}

export function dispatchLineClick(element, scales, evt, line, index = undefined) {
    element.dispatchEvent(
        new CustomEvent('line-click', {
            bubbles: true,
            detail: {
                evt,
                point: findDataPosition(element, scales, evt),
                metadata: line.metadata,
                target: line,
                index,
            },
        })
    );
}

export function dispatchLineHover(element, scales, evt, line, index = undefined) {
    element.dispatchEvent(
        new CustomEvent('line-hover', {
            bubbles: true,
            detail: {
                evt,
                point: findDataPosition(element, scales, evt),
                metadata: line.metadata,
                target: line,
                index,
            },
        })
    );
}
export function dispatchLineHoverEnd(element, evt, index = -1) {
    element.dispatchEvent(
        new CustomEvent('line-hover-end', {
            bubbles: true,
            detail: {
                evt,
                index,
            },
        })
    );
}
export function getDashAttributes(lineStyle: 'dashed' | 'dotted' | 'solid', lineWidth: number) {
    switch (lineStyle) {
        case 'dashed':
            return {
                'stroke-dasharray': '3 3',
            };
        case 'dotted':
            return {
                'stroke-dasharray': `0 ${lineWidth === 1 ? 3 : lineWidth * 2}`,
                'stroke-linecap': 'round',
            };
        case 'solid':
        default:
            return {
                'stroke-dasharray': 'initial',
            };
    }
}
