import { BaseElement, createClassName, defineElement, html, property } from '@umetrics/sartorius-ui-base';
import { spinnerStyles } from './spinnerStyle';
import { SpinnerProperties, SpinnerVariant } from './spinnerProperties';
import { version } from './metadata';

@defineElement({ tagName: 'spinner', styles: spinnerStyles, version })
export class Spinner extends BaseElement implements SpinnerProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property() size = '64px';
    @property({ type: String, reflect: false })
    label = '';
    @property({ type: SpinnerVariant, reflect: true })
    public variant? = SpinnerVariant.LIGHT_GREY;

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        const labelHtml =
            this.label !== '' ? html`<div class="${baseClassName}-label-${this.variant}">${this.label}</div>` : null;
        return html`
            <div class="${baseClassName}-wrapper">
                <svg
                    class="${rootClasses}"
                    width="${this.size}"
                    height="${this.size}"
                    viewBox="0 0 200 200"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <circle
                        class="${baseClassName}-circle-${this.variant}"
                        cx="100"
                        cy="100"
                        r="90"
                        fill="none"
                        stroke-width="25"
                    />
                    <path
                        class="${baseClassName}-arc-${this.variant}"
                        d=" M 100 10 A 90 90 0 0 1 190 100"
                        fill="none"
                        stroke-width="25"
                        shape-rendering="geometricPrecision"
                    />
                </svg>
                ${labelHtml}
            </div>
        `;
    }
}
