import React from "react";
import './DragDrop.css';
import {getSuiCheckboxReact, getSuiRadioGroupReact, getSuiRadioReact} from "@umetrics/studio-ui-design-system";
import {ComposablePlotEngineConfig} from "@umetrics/sartorius-ui-composable-plot-engine";
import Clicker, {ClickerProps} from "./Clicker";


const Radio = getSuiRadioReact();
const RadioGroup = getSuiRadioGroupReact();
const Checkbox = getSuiCheckboxReact();

interface ClickerMainProps extends ClickerProps {
    config: ComposablePlotEngineConfig;
    setConfig: (config: ComposablePlotEngineConfig) => void;
}

const ClickerMain: React.FC<ClickerMainProps> = ({config, setConfig, clicker, setClicker, onClose}) => {


    return (clicker && (
        <Clicker
            setClicker={setClicker}
            clicker={clicker}
            canDelete={false}
            onDelete={() => 0}
            onClose={onClose}>
            <Checkbox label={"Zoom"} checked={(config.main[0] as any).zoom} onChange={(e: any) => {
                (config.main[0] as any).zoom = !!e.target.checked ? {} : undefined;
                setConfig({...config});
            }}></Checkbox>

            <RadioGroup label={'Grid'}>
                <Radio value="1" checked={!config.main[0].grid} onChange={() => {
                    config.main[0].grid = undefined;
                    setConfig({...config});
                }}>Off</Radio>
                <Radio value="5" checked={config.main[0].grid?.mode === 'off'}
                       onChange={() => {
                           config.main[0].grid = {
                               ...config.main[0].grid,
                               mode: 'off'
                           } || {mode: 'off'};
                           setConfig({...config});
                       }}>Origo</Radio>
                <Radio value="2" checked={config.main[0].grid?.mode === 'default'}
                       onChange={() => {
                           config.main[0].grid = {
                               ...config.main[0].grid,
                               mode: 'default'
                           } || {mode: 'default'};
                           setConfig({...config});
                       }}>Default</Radio>
                <Radio value="3" checked={config.main[0].grid?.mode === 'vertical'}
                       onChange={() => {
                           config.main[0].grid = {
                               ...config.main[0].grid,
                               mode: 'vertical'
                           } || {mode: 'vertical'};
                           setConfig({...config});
                       }}>Vertical</Radio>
                <Radio value="4" checked={config.main[0].grid?.mode === 'horizontal'}
                       onChange={() => {
                           config.main[0].grid = {
                               ...config.main[0].grid,
                               mode: 'horizontal'
                           } || {mode: 'horizontal'};
                           setConfig({...config});
                       }}>Horizontal</Radio>
            </RadioGroup>
        </Clicker>
    ));
};
export default ClickerMain;