import { createContext, consume } from '@lit-labs/context';
import { BaseElement, property } from '@umetrics/sartorius-ui-base';
import { PlottableInterface } from '../plottable/plottableMixin';
import { Constructor } from './types';

export const dataContext = createContext<PlottableInterface[] | undefined>('data-context');

export declare abstract class WithDataContextInterface extends BaseElement {
    allDataElements?: PlottableInterface[];
}

export const WithDataContext = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class PlottableElement extends superClass {
        @consume({ context: dataContext, subscribe: true })
        @property({ attribute: false })
        allDataElements?: PlottableInterface[];
    }
    return PlottableElement as Constructor<WithDataContextInterface> & T;
};
