import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    property,
    PropertyValues,
    query,
    state,
    TemplateResult,
} from '@umetrics/sartorius-ui-base';
import { errorStyles } from './errorStyle';
import { DefaultMessage, ErrorProperties } from './errorProperties';
import { version } from './metadata';
import type { Collapse } from '@umetrics/sartorius-ui-collapse';

@defineElement({
    tagName: 'error',
    styles: errorStyles,
    version,
    //    sub: [Button, Collapse, Icon]
})
export class Error extends BaseElement implements ErrorProperties {
    // Private members
    private static Collapse;

    // Public members
    @property({ type: DefaultMessage, attribute: 'default-message' })
    defaultMessage;
    @property({ type: Boolean, attribute: 'no-extra' })
    noExtra = false;
    @property({ type: String, attribute: 'user-x' })
    userX = '';

    @state()
    private _small = false;
    private _observerAdded = false;
    private _collapseOpen = false;

    @query('[class$="-collapse"]')
    private _collapse!: Collapse;
    private _animationFrame: number | null = null;

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];

        imports.push({ module: () => import('@umetrics/sartorius-ui-collapse'), elem: 'Collapse' });

        Error.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    update(changedProperties: PropertyValues) {
        super.update(changedProperties);
        const root = this.shadowRoot!.querySelector('div');
        if (root) {
            this._updateSmall();
            if (!this._observerAdded) {
                new ResizeObserver(() => {
                    if (!this._animationFrame) {
                        this._animationFrame = window.requestAnimationFrame(() => {
                            this._animationFrame = null;
                            this._updateSmall();
                        });
                    }
                }).observe(root);
                this._observerAdded = true;
            }
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            small: this._small,
            openCollapse: this._collapseOpen,
        });
        let defaultValues: Record<string, string | TemplateResult> = {
            header: '',
            description: '',
            errorCode: '',
            information: '',
        };
        const supportLink = html`<a
            class="${baseClassName}-link"
            href="https://my.sartorius.com/"
            target="_blank"
            rel="noreferrer noopener"
            >my.sartorius.com</a
        >`;
        switch (this.defaultMessage) {
            case DefaultMessage.InternalError:
                defaultValues = {
                    header: 'Server Error',
                    description: html`Umetrics Studio has encountered an error. Try to refresh the page or contact
                    Sartorius Support at ${supportLink}`,
                    errorCode: '500',
                    information: 'Umetrics Studio Application Error - X was not able to load. Server not responding.',
                };
                break;
            case DefaultMessage.NotImplemented:
                defaultValues = {
                    header: 'Unknown Error',
                    description: html`Umetrics Studio has encountered an unknown error. Retry the action, refresh the
                    page or contact Sartorius Support at ${supportLink}`,
                    errorCode: '501',
                    information: '',
                };
                break;
            case DefaultMessage.NotAvailable:
                defaultValues = {
                    header: 'Service unavailable',
                    description: html`This service is temporarily unavailable due to undergoing maintenance or being
                    overloaded with high traffic. Retry the action later, refresh the page or contact Sartorius Support
                    at ${supportLink}`,
                    errorCode: '503',
                    information: '',
                };
                break;
            case DefaultMessage.Unauthorized:
                defaultValues = {
                    header: 'Unauthorized',
                    description: this.userX
                        ? html`It seems like you are unable to access this content. Contact the owner of the content,
                              ${this.userX}, to request access.<br />
                              <br />
                              Otherwise, try to refresh the page or contact Sartorius Support at ${supportLink}`
                        : html`It seems you are unable to access this content. Contact the owner of the content to
                              request access.<br />
                              <br />
                              Otherwise, try to refresh the page or contact Sartorius Support at ${supportLink}`,
                    errorCode: '401',
                    information: ``,
                };

                break;
            case DefaultMessage.Forbidden:
                defaultValues = {
                    header: 'Forbidden',
                    description: html`You don't have permission to access this page/resource`,
                    errorCode: '403',
                    information: '',
                };
                break;
            case DefaultMessage.NotFound:
                defaultValues = {
                    header: 'Page not found',
                    description: html`The page you are trying to access does not exist. Return to Home, refresh the page
                    or contact Sartorius Support at ${supportLink}`,
                    errorCode: '404',
                    information: '',
                };
                break;
        }

        const infoHtml = html` <slot name="information">${defaultValues.information}</slot>`;

        const buttonText = this._collapseOpen ? 'Hide details' : 'See details';

        const extraHtml = this.noExtra
            ? ''
            : html`
            <button @click="${this._handleCollapseToggle}" id="collapse-toggle" class="${baseClassName}-details-button">
                ${buttonText}
            </button>
            <${Error.Collapse.tag} class="${baseClassName}-collapse">
                  <div class="${baseClassName}-extra">
                      Error code: <slot name="error-code">${defaultValues.errorCode}</slot>
                      <br />
                      ${infoHtml}
                  </div> 
                </${Error.Collapse.tag}>
              `;

        return html`
            <div class="${rootClasses}">
                <div class="${baseClassName}-container">
                    <div class="${baseClassName}-header">
                        <slot name="header">${defaultValues.header}</slot>
                    </div>
                    <div class="${baseClassName}-description">
                        <slot name="description">${defaultValues.description}</slot>
                    </div>
                    ${extraHtml}
                    <div class="${baseClassName}-button">
                        <slot name="button"></slot>
                    </div>
                </div>
            </div>
        `;
    }

    private _handleCollapseToggle() {
        this._collapse.toggle();
        this._collapseOpen = !this._collapseOpen;
        this.requestUpdate();
    }

    private _updateSmall() {
        const root = this.shadowRoot!.querySelector('div');
        if (root) {
            this._small = root.offsetWidth < 500 || root.offsetHeight < 300;
        }
    }
}
