import React, {useCallback} from "react";
import './DragDrop.css';
import {
    getSuiCheckboxReact,
    getSuiColorPickerReact,
    getSuiDropdownReact,
    getSuiRadioGroupReact,
    getSuiRadioReact,
    getSuiTextfieldReact,
    TextFieldSize
} from "@umetrics/studio-ui-design-system";
import {ComposablePlotEngineConfig} from "@umetrics/sartorius-ui-composable-plot-engine";
import Clicker, {ClickerProps} from "./Clicker";
import SelectScale from "./SelectScale";
import {validateRuleForScaleChange} from "./configRules";

const Dropdown = getSuiDropdownReact();
const TextField = getSuiTextfieldReact();
const Radio = getSuiRadioReact();
const RadioGroup = getSuiRadioGroupReact();
const Checkbox = getSuiCheckboxReact();
const ColorPicker = getSuiColorPickerReact();

interface ClickerAxisProps extends ClickerProps {
    config: ComposablePlotEngineConfig;
    setConfig: (config: ComposablePlotEngineConfig) => void;
}

const ClickerAxis: React.FC<ClickerAxisProps> = ({config, setConfig, clicker, setClicker, onClose}) => {

    const onDelete = useCallback(() => {
        const updatedAxis = config.main[0]!.axis?.filter((s: any) => s.label !== clicker?.id);
        config.main[0]!.axis = updatedAxis ? [...updatedAxis] : [];
        setConfig({...config});
    }, [config, setConfig, clicker]);

    const axis = config.main[0].axis?.find(s => s.label === clicker?.id);
    const scale = config.scale.find(s => s.name === axis?.scaleName);

    return (clicker && (
        <Clicker
            setClicker={setClicker}
            clicker={clicker}
            canDelete={true}
            onDelete={onDelete}
            onClose={onClose}>
            <TextField label={"Label"}
                       placeholder={"Label"}
                       size={TextFieldSize.SMALL}
                       value={axis!.label}
                       onChange={(e: any) => {
                           axis!.label = e.target.value;
                           setConfig({...config});
                       }}></TextField>
            <div style={{display: "flex", flexDirection: "row", gap: "8px"}}>
                <SelectScale value={axis?.scaleName} orientation={scale?.orientation!}
                             onChange={(e: any) => {
                                 const newScale = e.target.value;
                                 const prevScale = axis!.scaleName;
                                 if (!validateRuleForScaleChange(config, newScale, prevScale)) {
                                     e.target.additionalFeedbackType = "failure";
                                     e.target.additionalFeedbackMessage = "Invalid scale change";
                                     return;
                                 }
                                 e.target.additionalFeedbackType = "";
                                 e.target.additionalFeedbackMessage = "";
                                 axis!.scaleName = newScale;
                                 setClicker(null); // need reopen
                                 setConfig({...config});
                             }} config={config}></SelectScale>

            </div>
            <div style={{display: "flex", flexDirection: "row", gap: "8px"}}>

                <ColorPicker label={"Axis Color"}
                             value={axis!.axisColor}
                             size={"small"}
                             onColorChange={e => {
                                 axis!.axisColor = e.detail.value;
                                 setConfig({...config});
                             }}></ColorPicker>
                <ColorPicker label={"Tick Color"}
                             value={axis!.tickColor}
                             size={"small"}
                             onColorChange={e => {
                                 axis!.tickColor = e.detail.value;
                                 setConfig({...config});
                             }}></ColorPicker>
                <ColorPicker label={"Label Color"}
                             value={axis!.labelColor}
                             size={"small"}
                             onColorChange={e => {
                                 axis!.labelColor = e.detail.value;
                                 setConfig({...config});
                             }}></ColorPicker>
            </div>
            <Checkbox label={"Symmetric"} checked={scale?.symmetric} onChange={(e: any) => {
                scale!.symmetric = !!e.target.checked;
                setConfig({...config});
            }}></Checkbox>
        </Clicker>
    ));
};
export default ClickerAxis;