import { cssSelector, cssVariableAsVar, GenerateStyles, style, css } from '@umetrics/sartorius-ui-base';

// see https://www.sartorius.com/brand-platform/applications/digital

export const spinnerStyles: GenerateStyles = (tagName) => {
    const { root, child } = cssSelector(tagName);

    const yellow = cssVariableAsVar('color_primary_1');
    const grey = cssVariableAsVar('color_secondary_3');
    const black = cssVariableAsVar('color_primary_3');

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: contents;
            }

            ${root} {
                overflow: visible;
                animation: rotate 1s infinite linear;
            }

            ${child('wrapper')} {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }

            ${child('label-light-grey')} {
                margin-top: 8px;
                color: ${black};
            }

            ${child('circle-light-grey')} {
                stroke: ${grey};
            }

            ${child('arc-light-grey')} {
                stroke: ${yellow};
            }

            ${child('label-dark-grey')} {
                margin-top: 8px;
                color: white;
            }

            ${child('circle-dark-grey')} {
                stroke: ${cssVariableAsVar('color_secondary_9')};
            }

            ${child('arc-dark-grey')} {
                stroke: ${yellow};
            }

            ${child('label-grey')} {
                margin-top: 8px;
                color: ${black};
            }

            ${child('circle-grey')} {
                stroke: ${grey};
            }

            ${child('arc-grey')} {
                stroke: ${cssVariableAsVar('color_secondary_9')};
            }

            @keyframes rotate {
                0% {
                    transform: rotate(0deg);
                }
                100% {
                    transform: rotate(359deg);
                }
            }
        `,
    ];
};
