import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    property,
    PropertyValues,
    state,
    svg,
} from '@umetrics/sartorius-ui-base';
import { Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { UUID } from '@umetrics/sartorius-ui-utils';
import { version } from '../../metadata';
import { WithContext } from '../../data/plotContext';
import { WithDataContext } from '../../data/dataContext';
import { LinearScale } from '../../data';
import { namespacedTag } from '../../tag';
import { WithInteractionContext } from './../interactionContext';
import { IndexToBoolFunctionType, ShapeStyle, SVGShapeProperties } from '../../plottable/svg/svgShape';
import { varToStyle } from '../../litUtils';
import {
    DataPoints,
    dispatchShapeCreate,
    dispatchShapeDoubleClick,
    dispatchShapeKeyDown,
    dispatchShapeMouseDown,
    dispatchShapeUpdate,
    DragMode,
    getHintTranslatePoint,
    getNewPointsOnDrag,
    Hint,
    isShape,
} from './shapeDrawingContextUtils';
import { shapeDrawingContextStyles } from './shapeDrawingContextStyles';

export type ShapeUpdateHandler = (event: CustomEvent) => void;
export type ShapeDoubleClickHandler = (index: number, event?: MouseEvent) => void;
export type MouseEventHandler = (event: MouseEvent) => void;

export enum ShapeType {
    Ellipse = 'Ellipse',
    Rectangle = 'Rectangle',
    BaseLine = 'BaseLine',
}

export interface ShapeProperties extends SVGShapeProperties {
    type: ShapeType;
    highlightStyle?: ShapeStyle;
    resizeTargetStyle?: ShapeStyle;
    customHints?: Hint[];
}

export interface ShapeDrawingContextProperties {
    newShapeType: ShapeType;
    data?: ShapeProperties[];
    onShapeCreate?: ShapeUpdateHandler;
    onShapeUpdate?: ShapeUpdateHandler;
    onShapeDelete?: ShapeUpdateHandler;
    onShapeDoubleClick?: ShapeDoubleClickHandler;
    onShapeKeyDown?: ShapeUpdateHandler;
    onShapeMouseDown?: ShapeUpdateHandler;
    isDrawing?: boolean;
}

interface ShapeDrawingProperties extends ShapeProperties {
    id: string;
}

@defineElement({ tagName: namespacedTag('svg-shape-drawing'), styles: shapeDrawingContextStyles, version })
export class ShapeDrawingContext
    extends WithInteractionContext(WithDataContext(WithContext(BaseElement)))
    implements ShapeDrawingContextProperties
{
    // Static members

    // Public members
    @property()
    newShapeType: ShapeType = ShapeType.BaseLine;
    @property()
    data: ShapeProperties[] = [];
    @property()
    onShapeCreate?: ShapeUpdateHandler;
    @property()
    onShapeUpdate?: ShapeUpdateHandler;
    @property()
    onShapeDelete?: ShapeUpdateHandler;
    @property()
    onShapeDoubleClick?: ShapeDoubleClickHandler;
    @property()
    isDrawing?: boolean = true;

    // Protected members

    // Private members
    @state()
    private _highlightedId?: string;
    @state()
    private _shapes: ShapeDrawingProperties[] = [];
    @state()
    private _newShape: ShapeDrawingProperties | null = null;

    private _drag?: {
        drag: boolean;
        prevX?: number;
        prevY?: number;
        mode?: DragMode;
    };

    constructor() {
        super();
        this._dispatchShapeKeyDown = this._dispatchShapeKeyDown.bind(this);
    }

    connectedCallback(): void {
        this.addEventListener('mousedown', this._handleMouseDown);
        this.addEventListener('mousemove', this._handleMouseMove);
        this.addEventListener('mouseup', this._handleMouseUp);

        document.addEventListener('keydown', this._dispatchShapeKeyDown);
        super.connectedCallback();
    }

    disconnectedCallback(): void {
        this.removeEventListener('mousedown', this._handleMouseDown);
        this.removeEventListener('mousemove', this._handleMouseMove);
        this.removeEventListener('mouseup', this._handleMouseUp);

        document.removeEventListener('keydown', this._dispatchShapeKeyDown);
        super.disconnectedCallback();
    }

    protected willUpdate(_changedProperties: PropertyValues<this>): void {
        super.willUpdate(_changedProperties);

        if (_changedProperties.has('data')) {
            this._shapes = this.data.map((shape) => {
                const id = UUID();
                if (shape.highlight) {
                    this._highlightedId = id;
                }

                return {
                    ...shape,
                    id,
                };
            });
        }
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});
        if (!this.size || !this.scaleMap) return;
        const [width, height] = this.size;

        return html`
            <svg width=${width} height=${height} class="${rootClasses}">
                ${this._shapes.map((shape: ShapeDrawingProperties) => this._renderShape(shape))}
                ${this._newShape && this._renderShape(this._newShape)}
            </svg>
        `;
    }

    private _getHoverTargetSvg(
        shapeType: ShapeType,
        data: {
            points: DataPoints;
        },
        baseClassName: string,
        dblClickHandler?: MouseEventHandler,
        mouseDownHandler?: MouseEventHandler
    ) {
        const [{ x: x1, y: y1 }, { x: x2, y: y2 }] = data.points as [Point2D, Point2D];
        const width = Math.abs(x1 - x2) / 2;
        const height = Math.abs(y1 - y2) / 2;
        const x = x1 > x2 ? x2 : x1;
        const y = y1 > y2 ? y2 : y1;

        switch (shapeType) {
            case ShapeType.Ellipse:
                return svg` <ellipse
                    cx=${x + width / 2}
                    cy=${y + height / 2}
                    rx=${width / 2}
                    ry=${height / 2}
                    class=${`${baseClassName}-hover-target`}
                    @dblclick=${dblClickHandler}
                    @mousedown=${mouseDownHandler}
                />`;
            case ShapeType.Rectangle:
                return svg` <rect
                    x=${x}
                    y=${y}
                    width=${width}
                    height=${height}
                    class=${`${baseClassName}-hover-target`}
                    @dblclick=${dblClickHandler}
                    @mousedown=${mouseDownHandler}
                />`;
            case ShapeType.BaseLine:
            default:
                return svg` <line
                    x1=${x1}
                    y1=${y1}
                    x2=${x2}
                    y2=${y2}
                    class=${`${baseClassName}-hover-target`}
                    @dblclick=${dblClickHandler}
                    @mousedown=${mouseDownHandler}
                />`;
        }
    }

    private _getCustomHints(points: DataPoints, scaledPoints: DataPoints, hints?: Hint[]) {
        if (!hints) {
            return [];
        }
        return hints.map((hint: Hint) => {
            const { xValue: cx, yValue: cy } = getHintTranslatePoint(
                points,
                scaledPoints,
                hint.position,
                hint.width,
                hint.height
            );
            if (!cx || !cy) {
                return;
            }

            const transform = `translate(${cx} ${cy})`;

            return svg`<g transform=${transform}>${hint.element}</svg>`;
        });
    }

    private _getShapeSvg(
        shapeType: ShapeType,
        data: {
            highlight: IndexToBoolFunctionType;
            points: DataPoints;
        },
        baseClassName: string
    ) {
        const [{ x: x1, y: y1 }, { x: x2, y: y2 }] = data.points as [Point2D, Point2D];
        const width = Math.abs(x1 - x2) / 2;
        const height = Math.abs(y1 - y2) / 2;
        const x = x1 > x2 ? x2 : x1;
        const y = y1 > y2 ? y2 : y1;
        const highlightClassName = data.highlight ? 'highlight' : '';

        switch (shapeType) {
            case ShapeType.Ellipse:
                return svg` <ellipse
                    cx=${x + width / 2}
                    cy=${y + height / 2}
                    rx=${width / 2}
                    ry=${height / 2}
                    class="${baseClassName}-target ${highlightClassName}"
                />`;
            case ShapeType.Rectangle:
                return svg` <rect
                    x=${x}
                    y=${y}
                    width=${width}
                    height=${height}
                    class="${baseClassName}-target ${highlightClassName}"
                />`;
            case ShapeType.BaseLine:
            default:
                return svg` <line
                    x1=${x1}
                    y1=${y1}
                    x2=${x2}
                    y2=${y2}
                    class="${baseClassName}-target ${highlightClassName}"
                />`;
        }
    }

    private _renderShape(shape: ShapeDrawingProperties) {
        const points = shape.data;
        const scales = this.getScales();
        if (!this.size || !scales || !points?.length || !isShape(points)) {
            return;
        }
        const variables = {
            '--shape-fill-color': (shape.defaultStyle as ShapeStyle)?.fillColor,
            '--shape-fill-opacity': (shape.defaultStyle as ShapeStyle)?.fillOpacity,
            '--shape-stroke-color': (shape.defaultStyle as ShapeStyle)?.strokeColor,
            '--shape-stroke-opacity': (shape.defaultStyle as ShapeStyle)?.strokeOpacity,
            '--shape-stroke-width': (shape.defaultStyle as ShapeStyle)?.strokeWidth,
            '--shape-stroke-dasharray': (shape.defaultStyle as ShapeStyle)?.strokeDashArray,

            '--shape-highlight-fill-color': shape.highlightStyle?.fillColor,
            '--shape-highlight-fill-opacity': shape.highlightStyle?.fillOpacity,
            '--shape-highlight-stroke-color': shape.highlightStyle?.strokeColor,
            '--shape-highlight-stroke-width': shape.highlightStyle?.strokeWidth,
            '--shape-highlight-stroke-opacity': shape.highlightStyle?.strokeOpacity,

            '--shape-hover-stroke': (shape.hoverStyle as ShapeStyle)?.strokeColor,
            '--shape-hover-stroke-opacity': (shape.hoverStyle as ShapeStyle)?.strokeOpacity,
            '--shape-hover-stroke-linecap': (shape.hoverStyle as ShapeStyle)?.strokeLinecap,
            '--shape-hover-stroke-width': (shape.hoverStyle as ShapeStyle)?.strokeWidth,

            '--shape-resize-stroke-width': shape.resizeTargetStyle?.strokeWidth,
            '--shape-resize-stroke': shape.resizeTargetStyle?.strokeColor,
        };

        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase());

        const [width, height] = this.size;
        const [scaleX, scaleY] = scales;

        const scaledPoints: DataPoints = [
            {
                x: scaleX(points[0].x),
                y: scaleY(points[0].y),
            },
            {
                x: scaleX(points[1].x),
                y: scaleY(points[1].y),
            },
        ];

        const endCircleRadius = 10;

        const shapeSvg = this._getShapeSvg(
            shape.type,
            {
                highlight: shape.highlight,
                points: scaledPoints,
            },
            baseClassName
        );

        let dblClickHandler: MouseEventHandler | undefined;
        let mouseDownHandler: MouseEventHandler | undefined;
        let handleEnd1MouseDown: MouseEventHandler | undefined;
        let handleEnd2MouseDown: MouseEventHandler | undefined;
        if (!this._newShape?.id || this._newShape?.id !== shape.id) {
            dblClickHandler = (event: MouseEvent) => this._handleShapeDblClick(event, shape.id);
            mouseDownHandler = (event: MouseEvent) => this._handleHoverTargetMouseDown(event, shape.id);
            handleEnd1MouseDown = this._handleEnd1MouseDown;
            handleEnd2MouseDown = this._handleEnd2MouseDown;
        }

        const hoverTargetSvg = shape.interactable
            ? this._getHoverTargetSvg(
                  shape.type,
                  { points: scaledPoints },
                  baseClassName,
                  dblClickHandler,
                  mouseDownHandler
              )
            : null;

        const resizeEnd1Svg =
            shape.interactable && shape.highlight && this.isDrawing
                ? svg` <circle
                  cx=${scaledPoints[0].x}
                  cy=${scaledPoints[0].y}
                  r=${endCircleRadius}
                  class=${`${baseClassName}-resize-target`}
                  @mousedown=${handleEnd1MouseDown}
              />`
                : null;

        const resizeEnd2Svg =
            shape.interactable && shape.highlight && this.isDrawing
                ? svg` <circle
                  cx=${scaledPoints[1].x}
                  cy=${scaledPoints[1].y}
                  r=${endCircleRadius}
                  class=${`${baseClassName}-resize-target`}
                  @mousedown=${handleEnd2MouseDown}
              />`
                : null;

        const customHints = this._getCustomHints(points, scaledPoints, shape.customHints);

        return html` <svg
            class="${baseClassName} ${rootClasses}"
            width=${width}
            height=${height}
            style=${varToStyle(variables)}
        >
            ${shapeSvg} ${hoverTargetSvg} ${customHints} ${resizeEnd1Svg} ${resizeEnd2Svg}
        </svg>`;
    }

    private _handleShapeDblClick(event: MouseEvent, clickedId: string) {
        const shape = this._shapes.find(({ id }) => id === clickedId);
        if (!shape) {
            return;
        }

        this._dispatchShapeDoubleClick(event, shape);
    }

    private _handleHoverTargetMouseDown(event: MouseEvent, shapeId: string) {
        this._dispatchShapeMouseDown(
            event,
            this._shapes.find(({ id }) => id === shapeId)
        );
        if (event.defaultPrevented) {
            return;
        }

        event.preventDefault();
        event.stopPropagation();

        this._highlightShape(shapeId);
        const invertedPosition = this._getIvertedPoint(event.clientX, event.clientY);
        this._drag = {
            prevX: invertedPosition.x,
            prevY: invertedPosition.y,
            drag: true,
            mode: DragMode.MoveLine,
        };
    }

    private _handleEnd1MouseDown(event: MouseEvent) {
        event.preventDefault();
        event.stopPropagation();

        const invertedPosition = this._getIvertedPoint(event.clientX, event.clientY);
        this._drag = {
            prevX: invertedPosition.x,
            prevY: invertedPosition.y,
            drag: true,
            mode: DragMode.ResizeEnd1,
        };
    }

    private _handleEnd2MouseDown(event: MouseEvent) {
        event.preventDefault();
        event.stopPropagation();

        const invertedPosition = this._getIvertedPoint(event.clientX, event.clientY);
        this._drag = {
            prevX: invertedPosition.x,
            prevY: invertedPosition.y,
            drag: true,
            mode: DragMode.ResizeEnd2,
        };
    }

    private _getIvertedPoint(x: number, y: number) {
        const [scaleX, scaleY] = this.getScales() as [LinearScale, LinearScale];
        return {
            x: scaleX.invert(x),
            y: scaleY.invert(y),
        };
    }

    private _startDrawingShape(event: MouseEvent) {
        const newPoint = this._getIvertedPoint(event.offsetX, event.offsetY);
        // happens if to go out of custom context boundaries and come back
        if (this._newShape?.data) {
            this._newShape = { ...this._newShape, data: [this._newShape.data[0], newPoint] };
        } else {
            this._newShape = {
                data: [newPoint, newPoint],
                type: this.newShapeType,
                defaultStyle: {
                    strokeColor: 'black',
                    strokeWidth: 2,
                },
                highlight: true,
                interactable: true,
                id: UUID(),
            };
        }
        this._highlightShape(this._newShape.id);
    }

    private _highlightShape(id?: string) {
        this._highlightedId = id;
        this._shapes = this._shapes.map((shape) => ({ ...shape, highlight: shape.id === id }));
    }

    private _drawShape(event: MouseEvent) {
        if (this._newShape?.data) {
            const endPoint = this._getIvertedPoint(event.offsetX, event.offsetY);
            this._newShape = { ...this._newShape, data: [this._newShape.data[0], endPoint] };
        }
    }

    private _endDrawingShape() {
        if (this._newShape?.data && isShape(this._newShape.data)) {
            this._shapes = [...this._shapes, this._newShape];
            this._dispatchShapeCreate(this._shapes, this._newShape);
        }
        this._newShape = null;
    }

    private _dragShape(clientX: number, clientY: number) {
        const oldShape = this._shapes.find(({ id }) => id === this._highlightedId);
        if (!this._drag?.drag || !oldShape?.data) return;

        const invertedPosition = this._getIvertedPoint(clientX, clientY);
        const deltaX = invertedPosition.x - (this._drag?.prevX ?? 0);
        const deltaY = invertedPosition.y - (this._drag?.prevY ?? 0);
        this._drag = {
            ...this._drag,
            prevX: invertedPosition.x,
            prevY: invertedPosition.y,
        };
        const oldPoints = oldShape.data;
        const newPoints = getNewPointsOnDrag(oldPoints, deltaX, deltaY, this._drag.mode);

        this._shapes = this._shapes.map((shape: ShapeDrawingProperties) =>
            shape.id === this._highlightedId
                ? {
                      ...shape,
                      data: newPoints,
                  }
                : shape
        );
    }

    private _handleMouseDown(event: MouseEvent) {
        event.preventDefault();
        event.stopPropagation();

        if (!this.isDrawing) {
            this._highlightShape();

            return;
        }

        if (this._newShape?.data) {
            this._endDrawingShape();
        } else {
            this._startDrawingShape(event);
        }
    }

    private _handleMouseMove(event: MouseEvent) {
        event.preventDefault();

        if (this._drag?.drag) {
            this._dragShape(event.clientX, event.clientY);
        } else if (this.isDrawing) {
            this._drawShape(event);
        }
    }

    private _handleMouseUp() {
        if (this._drag?.drag) {
            this._drag = undefined;

            const updatedShape = this._shapes.find(({ id }) => id === this._highlightedId);
            if (!updatedShape) {
                return;
            }

            this._dispatchShapeUpdate(this._shapes, updatedShape);
        } else if (this.isDrawing) {
            this._endDrawingShape();
        }
    }

    private _dispatchShapeDoubleClick(evt: MouseEvent, shape: ShapeDrawingProperties) {
        dispatchShapeDoubleClick(this, this.getScalesWithMeta(), evt, shape);
    }

    private _dispatchShapeCreate(newShapes: ShapeDrawingProperties[], shape: ShapeDrawingProperties) {
        dispatchShapeCreate(this, newShapes, shape);
    }

    private _dispatchShapeUpdate(newShapes: ShapeDrawingProperties[], shape: ShapeDrawingProperties) {
        dispatchShapeUpdate(this, newShapes, shape);
    }

    private _dispatchShapeKeyDown(event: KeyboardEvent) {
        dispatchShapeKeyDown(this, event, this._highlightedId && this._shapes[this._highlightedId]);
    }

    private _dispatchShapeMouseDown(event: MouseEvent, shape?: ShapeDrawingProperties) {
        dispatchShapeMouseDown(this, event, shape);
    }
}
