import React, {useCallback, useState} from 'react';
import './App.css';
import {
    type ComposablePlotEngineConfig,
    getSuiButtonReact,
    getSuiComposableInteractionContextReact,
    getSuiComposablePlotReact,
    getSuiComposableSVGBarReact,
    getSuiComposableSVGGridReact,
    getSuiComposableSVGLineReact,
    getSuiComposableSVGScatterReact,
    getSuiComposableZoomAndPanReact,
    getSuiContainerReact,
    getSuiHeaderReact,
    getSuiIconReact,
    getSuiMainReact,
    getSuiPanelTemplateReact,
    PartialScaleSettingsMap,
    reactifySartoriusUiComponent
} from "@umetrics/studio-ui-design-system";
import DragDrop from './DragDrop';
import DragDropAxis from './DragDropAxis';
import {GridMode} from "@umetrics/sartorius-ui-composable-plot/dist/plottable";
import AppNavbar from "./AppNavbar";
import AppLeft from "./AppLeft";
import AppRight from "./AppRight";
import ClickerMain from "./ClickerMain";
import ClickerAxis from "./ClickerAxis";
import ClickerShape from "./ClickerShape";
import type {
    ComposablePlotEngineConfigAxis,
    ComposablePlotEngineConfigMainStack,
    ComposablePlotEngineConfigScale
} from "@umetrics/sartorius-ui-composable-plot-engine";
import ClickerSerie from "./ClickerSerie";
import {
    ComposablePlotEngineConfigSerie
} from "@umetrics/sartorius-ui-composable-plot-engine/dist/composablePlotEngineProperties";
import {ZoomStrategy} from "@umetrics/sartorius-ui-composable-plot/dist/interaction/zoomBaseContext";
import {
    ShapeDrawingContext as ShapeDrawingContextWC,
    ShapeDrawingContextProperties,
    ShapeProperties,
    ShapeType
} from "@umetrics/sartorius-ui-composable-plot";

const PanelTemplate = getSuiPanelTemplateReact();

const Header = getSuiHeaderReact();

const Main = getSuiMainReact();
const Container = getSuiContainerReact();
const ComposablePlot = getSuiComposablePlotReact();

const Line = getSuiComposableSVGLineReact();
const Grid = getSuiComposableSVGGridReact();
const Scatter = getSuiComposableSVGScatterReact();
const Bar = getSuiComposableSVGBarReact();
const Button = getSuiButtonReact();
const Icon = getSuiIconReact();
const Interaction = getSuiComposableInteractionContextReact();
const ZoomAndPan = getSuiComposableZoomAndPanReact();
const ShapeDrawingContext = reactifySartoriusUiComponent<ShapeDrawingContextProperties>(ShapeDrawingContextWC.register(""));

const defaultConfig: ComposablePlotEngineConfig = {
    version: '0',
    scale: [],
    main: [{
        axis: [], serie: []
    }],
    data: {},
};
let dummyScaleY: ComposablePlotEngineConfigScale | null = null;
let dummyScaleX: ComposablePlotEngineConfigScale | null = null;

function App() {
    const [config, setConfig] = useState<ComposablePlotEngineConfig>(defaultConfig);
    //const [scaleSettings, setScaleSettings] = useState<PartialScaleSettingsMap>({});
    const [isMainSelected, setIsMainSelected] = useState(false);
    const [isElemSelected, setIsElemSelected] = useState("");
    const [clicker, setClicker] = useState<{ id: string, x: number, y: number } | null>(null);
    const [clickerAxis, setClickerAxis] = useState<{ id: string, x: number, y: number } | null>(null);
    const [clickerSerie, setClickerSerie] = useState<{ id: string, x: number, y: number } | null>(null);
    const [clickerShape, setClickerShape] = useState<{ id: string, x: number, y: number } | null>(null);
    const [clickerShapeWo, setClickerShapeWo] = useState<{ id: string, x: number, y: number } | null>(null);
    const [clickStart, setClickStart] = useState<{ id: string, x: number; y: number } | null>(null);

    const customSetConfig = useCallback((cfg: typeof config) => {
        console.log("New config : ", cfg);
        setConfig((JSON.parse(JSON.stringify(cfg))));
    }, []);

    const [zoomState, setZoomState] = useState({});
    const [shapeDrawing, setShapeDrawing] = useState<ShapeType | null>(null);
    const [shapes, setShapes] = useState<ShapeProperties[]>([]);

    React.useEffect(() => {
        if (clicker || clickerAxis || clickerSerie) {
            setClickerShapeWo(null);
        }
    }, [clicker, clickerAxis, clickerSerie, clickerShapeWo]);

    const onExport = useCallback(() => {
        const jsonString = JSON.stringify(config, null, 2); // Pretty-print JSON
        const blob = new Blob([jsonString], {type: "application/json"});
        const url = URL.createObjectURL(blob);
        const a = document.createElement("a");
        a.href = url;
        a.download = "data.json";
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url); // Clean up
    }, [config]);

    // Drag axis and drop on zone
    const onDropAxis = useCallback((axisKey: string, slot: string) => {
        const cfg = config;
        const axis = cfg.main[0].axis?.find(axis => axis.label === axisKey);
        if (axis) {
            axis.flip = !axis.flip;
            customSetConfig(cfg);
        }
    }, [config, customSetConfig]);

    // Drag asset and drop on zone
    const onDrop = useCallback((event: React.DragEvent<HTMLElement>, slot: string, obj: {
        id: string,
        data: number[]
    }) => {
        event.preventDefault();
        console.log("onDrop", event, slot, obj);

        const cfg: ComposablePlotEngineConfig = config || defaultConfig
        const main: ComposablePlotEngineConfigMainStack = cfg.main[0] as ComposablePlotEngineConfigMainStack;

        function addVariant(key: string, arr: string[]): string {
            let i = 0;
            let newKey = key;
            while (arr.includes(newKey)) {
                i++;
                newKey = `${key}${i}`;
            }
            return newKey;
        }

        function ensureScaleAndAxis(
            key: "x" | "y",
            oppositeKey: "y" | "x",
            orientation: "horizontal" | "vertical",
            dummyScale: ComposablePlotEngineConfigScale | null,
            cfg: ComposablePlotEngineConfig,
            obj: { id: string; data: number[] },
            slot: string,
            main: { axis?: ComposablePlotEngineConfigAxis[] }
        ): { newKey: string; scaleName: string; updatedDummyScale: ComposablePlotEngineConfigScale | null } {
            const existingScales = cfg.scale.filter(c => c.orientation === orientation).map(c => c.name);
            let oppositeScale = dummyScale || cfg.scale.find(c => c.orientation !== orientation);

            if (!oppositeScale) {
                oppositeScale = {
                    name: oppositeKey,
                    orientation: orientation === "horizontal" ? "vertical" : "horizontal",
                    domain: [0, 1]
                };
                dummyScale = oppositeScale;
            }

            console.log(`${oppositeKey.toUpperCase()} domain`, oppositeScale.domain);

            const newKey = addVariant(key, existingScales);
            cfg.scale.push({name: newKey, orientation});

            main.axis = main.axis || [];
            main.axis.push({
                scaleName: newKey,
                label: `${obj.id}`,
                flip: slot.endsWith("2"),
            });

            return {newKey, scaleName: oppositeScale.name, updatedDummyScale: dummyScale};
        }

        function ensureSerie(
            cfg: ComposablePlotEngineConfig,
            main: { serie?: ComposablePlotEngineConfigSerie[] },
            obj: { id: string; data: number[] },
            scalePrimary: string,
            scaleOpposite: string,
            isXAxis: boolean
        ) {
            main.serie = main.serie || [];

            // If dummy scale exists, update relevant series
            const dummyScale = isXAxis ? dummyScaleX : dummyScaleY;
            if (dummyScale) {
                main.serie.forEach(serie => {
                    if ((isXAxis ? serie.scaleX : serie.scaleY) === dummyScale.name) {
                        if (isXAxis) {
                            serie.scaleX = scalePrimary;
                            serie.dataX = obj.id;
                        } else {
                            serie.scaleY = scalePrimary;
                            serie.dataY = obj.id;
                        }
                        const {[scalePrimary]: removed, ...newData} = cfg.data!;
                        newData[obj.id] = obj.data;
                        cfg.data = newData;
                    }
                });

                if (isXAxis) dummyScaleX = null;
                else dummyScaleY = null;
                return;
            }

            // Find an existing serie using the opposite scale
            const existingSerie = main.serie.find(serie =>
                (isXAxis ? serie.scaleY : serie.scaleX) === scaleOpposite
            );
            const existingSerieData = (existingSerie?.scaleX === scaleOpposite ? existingSerie.dataX : existingSerie?.dataY) || scaleOpposite;

            if (!cfg.data) cfg.data = {};

            let evenlySpacedData;
            if (!cfg.data[existingSerieData]) {
                evenlySpacedData = generateEvenlySpacedArray(obj.data.length);
                cfg.data[existingSerieData] = evenlySpacedData;
            }

            if (!cfg.data[obj.id]) {
                cfg.data[obj.id] = obj.data;
            }

            // Add new serie
            main.serie.push({
                name: obj.id,
                type: "scatter",
                color: getRandomColor(),
                scaleX: isXAxis ? scalePrimary : scaleOpposite,
                scaleY: isXAxis ? scaleOpposite : scalePrimary,
                dataX: isXAxis ? obj.id : existingSerieData,
                dataY: isXAxis ? existingSerieData : obj.id,
            });
        }


        if (slot.startsWith('axis-x')) {
            const {newKey: newX, scaleName: ysName, updatedDummyScale} = ensureScaleAndAxis(
                "x", "y", "horizontal", dummyScaleY, cfg, obj, slot, main
            );
            dummyScaleY = updatedDummyScale;
            ensureSerie(cfg, main, obj, newX, ysName, true);

        } else {
            const {newKey: newY, scaleName: xsName, updatedDummyScale} = ensureScaleAndAxis(
                "y", "x", "vertical", dummyScaleX, cfg, obj, slot, main
            );
            dummyScaleX = updatedDummyScale;
            ensureSerie(cfg, main, obj, newY, xsName, false);

        }

        customSetConfig(cfg);
    }, [config, customSetConfig]);

    const barLen = config.main[0].serie!.filter(s => s.type === 'bar').length;
    let barNo = 0;

    return (
        <>
            <Header></Header>
            <AppNavbar onExport={onExport}></AppNavbar>
            <PanelTemplate>
                <AppLeft></AppLeft>
                <AppRight config={config} setConfig={customSetConfig} isElemSelected={isElemSelected}
                          isMainSelected={isMainSelected}></AppRight>
                <Main slot={"main-slot"}
                      style={{paddingTop: 8, paddingBottom: 8, background: "var(--color-secondary-2)",}}>


                    <Container noHeader style={{overflow: "hidden", resize: "both"}}>
                        <div style={{
                            height: "100%",
                            display: "flex",
                            flexDirection: "column",
                            gap: "12px",
                        }}>

                            <div style={{display: "flex", flexDirection: "row", justifyContent: "end"}}>
                                <Button compact type={"tertiary"} buttonSize={"small"}
                                        disabled={config.scale.length < 2}
                                        draggable
                                        onClick={e => setShapeDrawing(ShapeType.Rectangle)}
                                        onDragStart={e => console.log("dragstart")}>
                                    <Icon slot={"icon-slot"} icon={"checkboxUnchecked"}
                                    ></Icon>
                                </Button>

                                <Button compact type={"tertiary"} buttonSize={"small"}
                                        disabled={config.scale.length < 2}
                                        draggable
                                        onClick={e => setShapeDrawing(ShapeType.Ellipse)}
                                        onDragStart={e => console.log("dragstart")}>
                                    <Icon slot={"icon-slot"} icon={"timer"}
                                    ></Icon>
                                </Button>
                                <Button compact type={"tertiary"} buttonSize={"small"}
                                        disabled={config.scale.length < 2}
                                        draggable
                                        onClick={e => setShapeDrawing(ShapeType.BaseLine)}
                                        onDragStart={e => console.log("dragstart")}>
                                    <Icon slot={"icon-slot"} icon={"lineDashed"}></Icon>
                                </Button>
                            </div>

                            <div className="grid-container">
                                <div className="cell"></div>
                                <div className="cell"><DragDrop slot={"axis-x2"} width={"100%"} height={"32px"}
                                                                onDrop={onDrop}
                                                                onDropAxis={onDropAxis}></DragDrop></div>
                                <div className="cell"></div>

                                <div className="cell"><DragDrop slot={"axis-y"} width={"32px"} height={"100%"}
                                                                onDrop={onDrop}
                                                                onDropAxis={onDropAxis}></DragDrop></div>
                                <div className="cell">
                                    <ComposablePlot
                                        id={"the-plot"}
                                        className={"Plot"}
                                        scaleSettings={buildSaleSettings(config)}
                                        heading={config.heading}
                                        zoomState={zoomState}
                                    >
                                        <Interaction>
                                            {(config.main[0] as any).zoom &&
                                                <ZoomAndPan
                                                    zoomStrategy={ZoomStrategy.MultiAxis}
                                                    onPan={(e: any) => {
                                                        setZoomState(e.detail.zoomState);
                                                    }}
                                                    onZoom={(e: any) => {
                                                        setZoomState(e.detail.zoomState);
                                                    }}
                                                ></ZoomAndPan>}
                                            <ShapeDrawingContext isDrawing={true}
                                                                 data={config.main[0].shape ? config.main[0].shape.map(s => {
                                                                     //console.log("data:", s, config.data![s.data!]);
                                                                     const shapeStyle = {
                                                                         fillColor: s.fillColor,
                                                                         fillOpacity: s.fillOpacity,
                                                                         strokeColor: s.strokeColor,
                                                                         strokeOpacity: s.strokeOpacity,
                                                                         strokeLinecap: s.strokeLinecap,
                                                                         strokeDashArray: s.strokeDashArray,
                                                                     };
                                                                     return {
                                                                         type: s.type as ShapeType,
                                                                         id: s.shapeName,
                                                                         metadata: {
                                                                             name: s.shapeName,
                                                                         },
                                                                         interactable: true,
                                                                         highlight: clickerShapeWo?.id === s.shapeName,
                                                                         data: s.data ? config.data![s.data] : '',
                                                                         defaultStyle: shapeStyle,
                                                                         highlightStyle: shapeStyle,
                                                                         //resizeTargetStyle?: ShapeStyle;
                                                                         //customHints?: Hint[];
                                                                     };
                                                                 }) : []}
                                                                 newShapeType={shapeDrawing || ShapeType.Ellipse}
                                                                 onShapeCreate={(event: CustomEvent) => {
                                                                     const shape = event.detail.target;
                                                                     const newShapes: ShapeProperties[] = event.detail.newShapes;

                                                                     const cfg = config;
                                                                     if (!cfg.main[0].shape) {
                                                                         cfg.main[0].shape = [];
                                                                     }
                                                                     cfg.main[0].shape.push({
                                                                         shapeName: shape.id,
                                                                         data: shape.id,
                                                                         type: shapeDrawing as any,
                                                                     });
                                                                     cfg.data![shape.id] = shape.data;
                                                                     customSetConfig(cfg);
                                                                     setShapes(newShapes);
                                                                     setShapeDrawing(null);
                                                                 }}

                                                                 onShapeUpdate={e => {

                                                                     const cfg = config;
                                                                     const shape = e.detail.target;
                                                                     if (JSON.stringify(cfg.data![shape.id]) !== JSON.stringify(shape.data)) {
                                                                         cfg.data![shape.id] = shape.data;
                                                                         customSetConfig(cfg);
                                                                     }

                                                                 }}

                                                                 onShapeMouseDown={e => {
                                                                     setClickStart({
                                                                         id: e.detail.target.metadata.name,
                                                                         x: e.detail.evt.clientX,
                                                                         y: e.detail.evt.clientY
                                                                     });
                                                                     setClickerShapeWo({
                                                                         id: e.detail.target.metadata.name,
                                                                         x: e.detail.evt.clientX,
                                                                         y: e.detail.evt.clientY
                                                                     });

                                                                     console.log("mouseDownHandler", e, e.detail.target);


                                                                 }}
                                                                 onMouseUp={e => {
                                                                     if (!clickStart) return;

                                                                     const dx = Math.abs(clickStart.x - e.clientX);
                                                                     const dy = Math.abs(clickStart.y - e.clientY);
                                                                     const isClick = dx < 5 && dy < 5; // Detect small movement

                                                                     console.log("mouseup click", isClick);

                                                                     if (isClick) {
                                                                         /*if (clickerShape?.id === clickStart.id) {
                                                                             setClickerShape(null); // Clicking the same shape closes it
                                                                         } else {*/
                                                                         setClickerShape({
                                                                             id: clickStart.id,
                                                                             x: e.clientX,
                                                                             y: e.clientY,
                                                                         });
                                                                         //}
                                                                     }

                                                                     setClickStart(null);
                                                                 }}
                                                                 style={{
                                                                     zIndex: 1,
                                                                     pointerEvents: clickerShape ? "none" : shapeDrawing ? "all" : "none"
                                                                 }}
                                            ></ShapeDrawingContext>
                                        </Interaction>

                                        {(['horizontal', 'vertical'] as const).map((orientation) => {
                                            return config.main[0].axis!.filter(a => {
                                                return config.scale.some(s => s.orientation === orientation && s.name === a.scaleName)
                                            }).map(axis => {
                                                const flip = axis.flip!!;
                                                const slot = getAxisSlot(orientation, flip);

                                                return (<DragDropAxis
                                                    key={axis.label}
                                                    axis={axis}
                                                    slot={slot}
                                                    orientation={orientation}
                                                    flip={flip}
                                                    setClicker={setClickerAxis}

                                                    onDrop={(e, axisKey, draggedAxis, slot) => {

                                                        const data = e.dataTransfer.getData("text/plain");
                                                        if (data && data.startsWith("axis:")) { // Drag drop existing axis onto this axis
                                                            const draggedAxisKey = data!.split(":")[1];
                                                            const cfg = config;
                                                            const newAxisArray = moveScaleBefore(cfg.main[0].axis!, draggedAxisKey, axisKey,);
                                                            console.log("drop", axisKey, draggedAxisKey, newAxisArray, (cfg.main[0] as ComposablePlotEngineConfigMainStack).axis);
                                                            cfg.main[0].axis = newAxisArray;

                                                            const axis = cfg.scale.find(s => s.name === axisKey)!;
                                                            cfg.scale.filter(s => s.name === draggedAxisKey).forEach(s => s.orientation = axis.orientation);
                                                            customSetConfig({...cfg, ...{scale: cfg.scale}, ...{main: cfg.main}});
                                                            e.preventDefault();
                                                            return;
                                                        } else { // Drag drop new data onto the axis
                                                            let obj;
                                                            try {
                                                                obj = JSON.parse(data);
                                                            } catch {
                                                                console.log("drop junk");
                                                                return;
                                                            }


                                                            const cfg: ComposablePlotEngineConfig = config || defaultConfig
                                                            const main: ComposablePlotEngineConfigMainStack = cfg.main[0] as ComposablePlotEngineConfigMainStack;
                                                            const axis = main.axis?.find(a => a.label === axisKey);
                                                            const serie = main.serie?.find(s => s.scaleY === axis?.scaleName || s.scaleX === axis?.scaleName);
                                                            const newSerie = {...serie!};
                                                            if (newSerie.scaleX === axis?.scaleName) {
                                                                newSerie.dataX = obj.id;
                                                            } else if (newSerie.scaleY === axis?.scaleName) {
                                                                newSerie.dataY = obj.id;
                                                            }
                                                            newSerie.name = obj.id;
                                                            newSerie.color = getRandomColor();
                                                            main.serie?.push(newSerie);
                                                            cfg.data![obj.id] = obj.data

                                                            customSetConfig(cfg);

                                                        }

                                                    }}
                                                />);
                                            })
                                        })}

                                        {shapeDrawing === null && <div id="the-main"
                                                                       className={`Main${isMainSelected ? ' selected' : ""}`}
                                                                       tabIndex={0} // Could also you click to select and document.click to deselect
                                                                       onFocus={() => setIsMainSelected(true)}
                                                                       onBlur={() => setIsMainSelected(false)}
                                                                       onClick={e => {
                                                                           setClicker({
                                                                               id: "0",
                                                                               x: e.clientX,
                                                                               y: e.clientY
                                                                           });
                                                                       }}
                                                                       onDragOver={(e) => {
                                                                           console.log("dragOver");
                                                                           e.preventDefault();
                                                                       }}
                                                                       onDrop={(e) => {
                                                                           console.log("drop");
                                                                           e.preventDefault();
                                                                       }}
                                        ></div>}

                                        {config && config.main[0] && config.main[0].grid && (
                                            <Grid scaleX={'x'} scaleY={'y'} includeInDomain={false}
                                                  grid={config.main[0].grid.mode as GridMode}></Grid>
                                        )
                                        }


                                        {config && config.main[0] && config.main[0].serie!.map(serie => {
                                            if (serie.scaleX && serie.scaleY && serie.dataX && serie.dataY && config.data) {

                                                if (serie.type === 'bar') {

                                                    return (
                                                        <Bar key={serie.name} scaleX={serie.scaleX}
                                                             scaleY={serie.scaleY}
                                                             data={formatPlotData(serie.dataX, serie.dataY, config.data)}
                                                             tabIndex={0} // Could also you click to select and document.click to deselect
                                                             highlight={isElemSelected === serie.name}
                                                             defaultStyle={{
                                                                 fillColor: serie.color as string,
                                                                 strokeColor: serie.color as string
                                                             }}
                                                             targetColor={isElemSelected === serie.name ? "#96d1fb" : '#000'} // not 000, from cfg
                                                             targetOpacity={isElemSelected === serie.name ? 0.5 : 0.2} // not 0.2, from cfg
                                                             group={{
                                                                 index: barNo++,
                                                                 totalCount: barLen,
                                                                 //spacing: 2,
                                                                 offsetY: undefined,
                                                             }}
                                                             onClick={e => {
                                                                 setClickerSerie({
                                                                     id: serie.name,
                                                                     x: e.clientX,
                                                                     y: e.clientY
                                                                 });
                                                             }} style={{zIndex: 3}}></Bar>
                                                    );
                                                } else if (serie.type === 'line') {
                                                    return (
                                                        <Line key={serie.name} scaleX={serie.scaleX}
                                                              scaleY={serie.scaleY}
                                                              data={formatPlotData(serie.dataX, serie.dataY, config.data)}
                                                              tabIndex={0} // Could also you click to select and document.click to deselect
                                                              highlight={isElemSelected === serie.name}
                                                              defaultStyle={{
                                                                  strokeColor: serie.color as string
                                                              }}
                                                              targetColor={isElemSelected === serie.name ? "#96d1fb" : '#000'} // not 000, from cfg
                                                              targetOpacity={isElemSelected === serie.name ? 0.5 : 0.2} // not 0.2, from cfg
                                                              onFocus={() => setIsElemSelected(serie.name)}
                                                              onBlur={() => setIsElemSelected("")}
                                                              onClick={e => {
                                                                  setClickerSerie({
                                                                      id: serie.name,
                                                                      x: e.clientX,
                                                                      y: e.clientY
                                                                  });
                                                              }}
                                                              style={{zIndex: 3}}
                                                        ></Line>
                                                    );
                                                } else if (serie.type === 'scatter') {
                                                    const category = serie.colorBy ? config.data[serie.colorBy] : null;
                                                    return (
                                                        <Scatter key={serie.name} scaleX={serie.scaleX}
                                                                 scaleY={serie.scaleY}
                                                                 data={formatPlotData(serie.dataX, serie.dataY, config.data)}
                                                                 tabIndex={0} // Could also you click to select and document.click to deselect
                                                                 defaultStyle={({index}) => {
                                                                     return {
                                                                         fillColor:
                                                                             typeof serie.color === 'object'
                                                                                 ? serie.color[category![index] as string]
                                                                                 : serie.color,
                                                                     };
                                                                 }}
                                                                 highlight={isElemSelected === serie.name}
                                                                 targetColor={isElemSelected === serie.name ? "#96d1fb" : '#000'} // not 000, from cfg
                                                                 targetOpacity={isElemSelected === serie.name ? 0.5 : 0.2} // not 0.2, from cfg
                                                                 onFocus={() => setIsElemSelected(serie.name)}
                                                                 onBlur={() => setIsElemSelected("")}
                                                                 onClick={e => {
                                                                     setClickerSerie({
                                                                         id: serie.name,
                                                                         x: e.clientX,
                                                                         y: e.clientY
                                                                     });
                                                                 }}
                                                                 onDragOver={e => e.preventDefault()}
                                                                 onDrop={e => {
                                                                     const obj = JSON.parse(e.dataTransfer.getData("text/plain"))
                                                                     if (obj && obj.id) {
                                                                         const cfg = config;
                                                                         e.preventDefault();
                                                                         serie.colorBy = obj.id;

                                                                         serie.color = obj.data.reduce((acc: Record<string, string>, key: string) => {
                                                                             acc[key] = acc[key] || getRandomColor();
                                                                             return acc;
                                                                         }, {} as Record<string, string>);

                                                                         if (!Object.keys(cfg.data!).includes(obj.id)) {
                                                                             cfg.data![obj.id] = obj.data;
                                                                         }
                                                                         customSetConfig(cfg);
                                                                     }

                                                                 }}
                                                                 style={{zIndex: 3}}
                                                        ></Scatter>
                                                    );
                                                } else if (serie.type === 'line-scatter') {
                                                    return (
                                                        <>
                                                            <Line key={serie.name + "line"} scaleX={serie.scaleX}
                                                                  scaleY={serie.scaleY}
                                                                  data={formatPlotData(serie.dataX, serie.dataY, config.data)}
                                                                  tabIndex={0} // Could also you click to select and document.click to deselect
                                                                  highlight={isElemSelected === serie.name}
                                                                  defaultStyle={{
                                                                      strokeColor: serie.color as string
                                                                  }}
                                                                  targetColor={isElemSelected === serie.name ? "#96d1fb" : '#000'} // not 000, from cfg
                                                                  targetOpacity={isElemSelected === serie.name ? 0.5 : 0.2} // not 0.2, from cfg
                                                                  onFocus={() => setIsElemSelected(serie.name)}
                                                                  onBlur={() => setIsElemSelected("")}
                                                                  onClick={e => {
                                                                      setClickerSerie({
                                                                          id: serie.name,
                                                                          x: e.clientX,
                                                                          y: e.clientY
                                                                      });
                                                                  }}
                                                                  style={{zIndex: 3}}
                                                            ></Line>
                                                            <Scatter key={serie.name + "scatter"} scaleX={serie.scaleX}
                                                                     scaleY={serie.scaleY}
                                                                     data={formatPlotData(serie.dataX, serie.dataY, config.data)}
                                                                     tabIndex={0} // Could also you click to select and document.click to deselect
                                                                     defaultStyle={{
                                                                         //strokeColor: serie.color
                                                                         fillColor: serie.color as string,
                                                                     }}
                                                                     highlight={isElemSelected === serie.name}
                                                                     targetColor={isElemSelected === serie.name ? "#96d1fb" : '#000'} // not 000, from cfg
                                                                     targetOpacity={isElemSelected === serie.name ? 0.5 : 0.2} // not 0.2, from cfg
                                                                     onFocus={() => setIsElemSelected(serie.name)}
                                                                     onBlur={() => setIsElemSelected("")}
                                                                     onClick={e => {
                                                                         setClickerSerie({
                                                                             id: serie.name,
                                                                             x: e.clientX,
                                                                             y: e.clientY
                                                                         });
                                                                     }}
                                                                     style={{zIndex: 3}}></Scatter>
                                                        </>
                                                    );
                                                }
                                            }
                                        })}
                                    </ComposablePlot>
                                    {clicker &&
                                        <ClickerMain config={config} setConfig={customSetConfig} clicker={clicker}
                                                     setClicker={setClicker}></ClickerMain>}
                                    {clickerAxis &&
                                        <ClickerAxis config={config} setConfig={customSetConfig} clicker={clickerAxis}
                                                     setClicker={setClickerAxis}></ClickerAxis>}
                                    {clickerSerie &&
                                        <ClickerSerie config={config} setConfig={customSetConfig} clicker={clickerSerie}
                                                      setClicker={setClickerSerie}></ClickerSerie>}
                                    {clickerShape &&
                                        <ClickerShape config={config} setConfig={customSetConfig} clicker={clickerShape}
                                                      setClicker={setClickerShape}></ClickerShape>}
                                </div>
                                <div className="cell"><DragDrop slot={"axis-y2"} width={"32px"} height={"100%"}
                                                                onDrop={onDrop}
                                                                onDropAxis={onDropAxis}></DragDrop></div>

                                <div className="cell"></div>
                                <div className="cell"><DragDrop slot={"axis-x"} width={"100%"} height={"32px"}
                                                                onDrop={onDrop}
                                                                onDropAxis={onDropAxis}></DragDrop></div>
                                <div className="cell"></div>
                            </div>
                        </div>
                    </Container>

                </Main>
            </PanelTemplate>
        </>
    )
        ;
}

function formatPlotData(
    dataX: string,
    dataY: string,
    plotData: { [key: string]: (any) }[] | { [key: string]: (any)[] },
    groupBy?: Datum[]
) {
    if (!Array.isArray(plotData) && Array.isArray(plotData[dataX]) && Array.isArray(plotData[dataY])) {
        // If in object format { x: [], y: [] }, map it to [{x, y}]
        const formattedData = plotData[dataX].map((xValue, index) => ({
            x: xValue,
            y: plotData[dataY][index],
            group: groupBy ? groupBy[index] : null, // Attach group if provided
        }));

        return groupBy ? _groupBy(formattedData) : formattedData;
    } else if (
        Array.isArray(plotData) &&
        plotData.every((el) => typeof el === 'object' && el[dataX] != null && el[dataY] != null)
    ) {
        // already in the format [{x, y}]
        const formattedData = plotData.map(({[dataX]: x, [dataY]: y}, index) => ({
            x,
            y,
            group: groupBy ? groupBy[index] : null, // Attach group if provided
        }));

        return groupBy ? _groupBy(formattedData) : formattedData;
    } else {
        throw new Error(`Invalid data format ${dataX} ${dataY}`);
    }
}

function old_formatPlotData(
    scaleX: string,
    scaleY: string,
    plotData: { [key: string]: (any) }[] | { [key: string]: (any)[] },
) {
    if (!Array.isArray(plotData) && Array.isArray(plotData[scaleX]) && Array.isArray(plotData[scaleY])) {
        // If in object format { x: [], y: [] }, map it to [{x, y}]
        return plotData[scaleX].map((xValue, index) => ({
            x: xValue,
            y: plotData[scaleY as string][index],
        }));
    } else if (
        Array.isArray(plotData) &&
        plotData.every((el) => typeof el === 'object' && el.hasOwnProperty(scaleX) && el.hasOwnProperty(scaleY))
    ) {
        // already in the format [{x, y}]
        return plotData.map(({[scaleX]: x, [scaleY]: y}) => ({x, y}));
    } else {
        throw new Error('Invalid data format');
    }
}

type Datum = string | number;

function _groupBy(data: { x: Datum; y: Datum; group: Datum | null }[]): { x: Datum; y: Datum; group?: Datum }[][] {
    const groupedMap = new Map<Datum, { x: Datum; y: Datum; group?: Datum }[]>();

    data.forEach(({x, y, group}) => {
        if (group == null) return; // Skip if no group defined
        if (!groupedMap.has(group)) groupedMap.set(group, []);
        groupedMap.get(group)!.push({x, y, group});
    });

    return Array.from(groupedMap.values());
}

function getAxisSlot(orientation: 'horizontal' | 'vertical', flip: boolean): string {
    return (orientation === 'horizontal' ? 'axis-x' : 'axis-y') + (flip ? "2" : '');
}

function moveScaleBefore(scales: ComposablePlotEngineConfigAxis[], scaleToMove: string, targetScale: string) {
    const scaleMap = new Map(scales.map(scale => [scale.scaleName, scale]));
    if (!scaleMap.has(scaleToMove) || !scaleMap.has(targetScale)) {
        return scales;
    }
    const fromIndex = scales.findIndex(scale => scale.scaleName === scaleToMove);
    const toIndex = scales.findIndex(scale => scale.scaleName === targetScale);
    if (fromIndex === -1 || toIndex === -1 || fromIndex === toIndex) {
        return scales;
    }
    const newScales = [...scales];
    if (fromIndex < toIndex) {
        newScales.splice(toIndex + 1, 0, newScales.splice(fromIndex, 1)[0]);
    } else {
        newScales.splice(toIndex, 0, newScales.splice(fromIndex, 1)[0]);
    }
    return newScales;
}

function generateEvenlySpacedArray(size: number) {
    if (size <= 1) return size === 1 ? [0] : []; // Handle edge cases
    return Array.from({length: size}, (_, i) => i / (size - 1));
}

function getRandomColor(): string {
    const maxBrightness = 0xAAAAAA;
    return `#${Math.floor(Math.random() * maxBrightness).toString(16).padStart(6, '0')}`;
}

function buildSaleSettings(cfg: ComposablePlotEngineConfig) {
    const main = cfg.main[0];

    const scaleSettings: PartialScaleSettingsMap = Object.fromEntries(
        [...cfg.scale, ...(dummyScaleX ? [dummyScaleX] : []), ...(dummyScaleY ? [dummyScaleY] : [])].map(s => {
            const scaleKey = s.name;
            const scaleType = s.scaleType;
            const domain = s.domain;

            const gotData =
                cfg.main &&
                !Array.isArray(main) &&
                Object.values(main.serie ?? {}).some((t) => t.scaleX === scaleKey || t.scaleY === scaleKey);

            const setting: NonNullable<PartialScaleSettingsMap>[string] = {
                orientation: s.orientation,
                padding: {
                    type: 'pixel',
                    start: 5,
                    end: 5,
                },
                symmetricDomain: s.symmetric,
            };

            if (scaleType || !gotData) {
                setting.scaleType = scaleType ?? 'linear';
            }

            if (domain || !gotData) {
                setting.domain = domain ?? [0, 1];
            }
            return [scaleKey, setting];
        })
    );
    return scaleSettings;
}

export default App;
