import { cssVariables } from './variables';
import { CSSResult, unsafeCSS } from '@lit/reactive-element/css-tag.js';

export type CSSVariable = keyof typeof cssVariables;

/**
 * Construct a var(--variable) pattern ready to be used in css.
 *
 * @param v
 */
export const cssVariableAsVar = (v: CSSVariable): CSSResult => {
    return unsafeCSS(`var(--${v.replace(/_/g, '-')})`);
};

/**
 *  Extract only value of the variable. Useful when for example passing value as string arg to a function
 *  that cant handle var(--variable).
 *
 *  @param v - the variable
 */
export const cssVariableValue = (v: CSSVariable): CSSResult => {
    return unsafeCSS(`${cssVariables[v]}`);
};
