import { Domain } from '../data';

export const calculateNewDomain = (
    [min, max]: [number, number],
    pivot: number,
    deltaZoom: number,
    zoomLimit: number,
    bounds: [number, number]
): Domain => {
    const newMin = (min - pivot) / deltaZoom + pivot;
    const newMax = (max - pivot) / deltaZoom + pivot;
    const range = Math.abs(newMax - newMin);

    if (range < zoomLimit) {
        // If the range is smaller than the zoom limit, adjust to the zoom limit
        const center = (max + min) / 2;
        const halfRange = zoomLimit / 2;
        return [center - halfRange, center + halfRange];
    } else {
        // Ensure the new domain doesn't exceed the bounds
        const boundedMin = Math.min(bounds[1] * 0.99, Math.max(bounds[0], newMin));
        const boundedMax = Math.max(bounds[0] * 0.99, Math.min(bounds[1], newMax));
        return [boundedMin, boundedMax];
    }
};

export function assertDefined<T>(value: T | null | undefined, msg = ''): asserts value is NonNullable<T> {
    if (value == null) {
        throw new Error(`Expects that provided value is defined. ${msg}`);
    }
}
