import {useState} from "react";
import './DragDrop.css';

interface DragDropProps {
    width: string;
    height: string;
    slot: string;
    onDrop: (e: React.DragEvent<HTMLElement>, slot: string, obj: { id: string, data: number[] }) => void;
    onDropAxis: (axisKey: string, slot: string) => void;
}

const DragDrop: React.FC<DragDropProps> = ({width, height, slot, onDrop, onDropAxis}) => {

    const [isDragging, setIsDragging] = useState(false);

    const handleDragOver = (event: React.DragEvent<HTMLDivElement>) => {
        /*const obj = JSON.parse(event.dataTransfer.getData("text/plain"));
        if (!obj || !obj.id || !obj.data) {
            return;
        }*/
        event.preventDefault();
        setIsDragging(true);
    };

    const handleDragLeave = () => {
        setIsDragging(false);
    };

    const handleDrop = (event: React.DragEvent<HTMLElement>) => {
        setIsDragging(false);
        let obj;
        const data = event.dataTransfer.getData("text/plain");


        if (data && data.startsWith("axis:")) { // Drag drop existing axis on the drop zone
            const axis = data.split(":");
            onDropAxis(axis[1], slot);
            event.preventDefault();
            return;
        } else { // Drag drop data on the drop zone
            try {
                obj = JSON.parse(data);
            } catch {
                console.log("drop junk");
                return;
            }
        }
        if (!obj || !obj.id || !obj.data) {
            console.log("drop unknown");
            return;
        }

        event.preventDefault();

        console.log("Dropped!", obj.id, obj.data);
        onDrop(event, slot, obj);
    };

    return (
        <div
            className={`drop-zone ${isDragging ? "drag-over" : ""}`}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
            slot={slot}
            style={{
                width,
                height,

            }}
            title={"Drop zone"}
        >
        </div>
    );
};
export default DragDrop;