import { PropertyValues } from '@umetrics/sartorius-ui-base';

export const anyChanged = <T, K extends keyof T>(changedProps: PropertyValues<T>, keys: K[]) => {
    return keys.some((k) => changedProps.has(k));
};

/**
 * Helper that transform key value to css variable format. Key will be prefixed with -- if not already there.
 * @param key : string like "key" or "--key"
 * @param val : string "value"
 * @return string : "--key: value;"
 */
export const keyValToVar = (key: string, val: string | number): string =>
    (key.startsWith('--') ? key : `--${key}`) + `: ${val};`;

/**
 * Helper that transform keys as css variable names and values as css value if set to style string.
 * @param obj : map {"--key1": value, "--key2": null}
 * @return string : "--key1: value;"
 */
export const varToStyle = (obj: { [key: string]: any }): string =>
    Object.entries(obj)
        .filter(([k, v]) => k && v)
        .map(([k, v]) => keyValToVar(k, v))
        .join('');
