import React from "react";
import './DragDrop.css';
import {getSuiListItemReact, getSuiListReact, getSuiPaneReact} from "@umetrics/studio-ui-design-system";
import usp from "./usp.json";

const Pane = getSuiPaneReact();
const List = getSuiListReact();
const ListItem = getSuiListItemReact();

interface AppLeftProps {

}

const AppLeft: React.FC<AppLeftProps> = ({}) => {


    return (
        <Pane active slot={"left-slot"} position="left">
            <div slot={"header-slot"}>USP</div>
            <List>
                <ListItem className={"BigListItem"} key={"model"} expanded><span
                    slot={"item-text-slot"}>M1 Model</span>
                    {usp.models[0].scores.series_names.map(m => {
                        const missing = m.values.filter(v => v == null).length;
                        const categoric = m.values.some(v => typeof v === 'string');
                        let domain;
                        if (!categoric) {
                            const min = Math.min(...(m.values as number[]));
                            const max = Math.max(...(m.values as number[]));
                            domain = `[${min.toFixed(1)} - ${max.toFixed(1)}]`;
                        } else {
                            const set = new Set(m.values as any[]);
                            domain = `Categoric(${set.size})`;
                        }
                        const data = JSON.stringify({id: m.id, data: m.values});
                        return (
                            <ListItem key={m.id} dataTransfer={data}
                                      className={"ListItem"} clickable draggable title={data}><span
                                slot={"item-text-slot"} style={{pointerEvents: "none"}}>{m.id}</span>
                                <span slot={"metadata-slot"}
                                      style={{pointerEvents: "none", display: "flex", gap: "4px"}}>
                                            {<span>{domain}</span>}
                                    <span>{m.values.length}</span>
                                    {missing > 0 &&
                                        <span slot={"metadata-slot"}
                                              style={{color: 'red'}}>({missing} missing)</span>}
                                        </span>
                            </ListItem>
                        );
                    })}
                </ListItem>
                <ListItem className={"BigListItem"} key={"dataset"} expanded><span
                    slot={"item-text-slot"}>Dataset</span>
                    {usp.datasets[0].series_names.map(m => {
                        const missing = (m.values as any[]).filter(v => v == null).length;
                        const categoric = m.values.some(v => typeof v === 'string');
                        let domain;
                        if (!categoric) {
                            const min = Math.min(...(m.values as number[]));
                            const max = Math.max(...(m.values as number[]));
                            domain = `[${min.toFixed(1)} - ${max.toFixed(1)}]`;
                        } else {
                            const set = new Set(m.values as any[]);
                            domain = `Categoric(${set.size})`;
                        }
                        const data = JSON.stringify({id: m.id, data: m.values});
                        return (
                            <ListItem key={m.id} dataTransfer={data}
                                      className={"ListItem"} clickable draggable title={data}><span
                                slot={"item-text-slot"} style={{pointerEvents: "none"}}>{m.id}</span>
                                <span slot={"metadata-slot"}
                                      style={{pointerEvents: "none", display: "flex", gap: "4px"}}>

                                            {<span>{domain}</span>}
                                    <span>{m.values.length}</span>
                                    {missing > 0 &&
                                        <span slot={"metadata-slot"}
                                              style={{color: 'red'}}>({missing} missing)</span>}
                                        </span>
                            </ListItem>
                        );
                    })}
                </ListItem>
            </List>
        </Pane>
    );
};
export default AppLeft;