export const groupBy = <T>(arr: T[], keyFn: (val: T) => string): Record<string, T[]> =>
    arr.reduce(
        (acc, cur) => ({
            ...acc,
            [keyFn(cur)]: acc[keyFn(cur)] ? [...acc[keyFn(cur)], cur] : [cur],
        }),
        {}
    );

export const pipe =
    <T>(...fns: Array<(arg: T) => T>) =>
    (value: T) =>
        fns.reduce((acc, fn) => fn(acc), value);

export const mapValues = <A, B>(obj: Record<string, A>, fn: (val: A) => B): Record<string, B> =>
    Object.fromEntries(Object.entries(obj).map(([key, val]) => [key, fn(val)]));
export const mapEntries = <A, B>(obj: Record<string, A>, fn: (val: [string, A]) => B): Record<string, B> =>
    Object.fromEntries(Object.entries(obj).map(([key, val]) => [key, fn([key, val])]));
export const mapKeys = <A>(obj: Record<string, A>, fn: (key: string) => string): Record<string, A> =>
    Object.fromEntries(Object.entries(obj).map(([key, val]) => [fn(key), val]));
export const isDefined = <A>(obj?: Record<string, A>): boolean => Boolean(obj && Object.keys(obj).length);

export const filterEntries = <A>(obj: Record<string, A>, fn: (val: [string, A]) => boolean): Record<string, A> =>
    Object.fromEntries(Object.entries(obj).filter((entry) => fn(entry)));

export const getRectOffset = (rectA: DOMRect, rectB: DOMRect) => [rectA.left - rectB.left, rectA.top - rectB.top];
