const typesEquals = (a, b, type) => typeof a === type && typeof b === type;

function deepEqualArrays<T>(a: Array<T>, b: Array<T>) {
    if (a.length !== b.length) return false; // Different length, not equal
    for (let i = 0; i < a.length; i++) {
        // eslint-disable-next-line @typescript-eslint/no-use-before-define
        if (!deepEqual(a[i], b[i])) return false; // Deep comparison of array elements
    }
    return true;
}

function deepEqualObjects(a, b) {
    const keysA = Object.keys(a);
    const keysB = Object.keys(b);
    if (keysA.length !== keysB.length) return false; // Different number of keys, not equal
    for (const key of keysA) {
        // eslint-disable-next-line @typescript-eslint/no-use-before-define
        if (!keysB.includes(key) || !deepEqual(a[key], b[key])) {
            return false; // Key missing or value differs
        }
    }
    return true;
}

function deepEqual<T>(a: T, b: T): boolean {
    // Check if both are strictly equal (===), including handling of primitives
    if (a === b) return true;

    // If either is null/undefined, they are not equal
    if (a == null || b == null) return false;

    // Handle arrays
    if (Array.isArray(a) && Array.isArray(b)) {
        return deepEqualArrays(a, b);
    }

    // Handle objects
    if (typesEquals(a, b, 'object')) {
        return deepEqualObjects(a, b);
    } else if (typesEquals(a, b, 'function')) {
        return a.toString() === b.toString();
    }

    // For primitives, compare directly
    return false;
}

export const dataChangeDetection = <T>(value: T, oldValue: T): boolean => {
    // Use deepEqual directly to determine if values are equal
    return !deepEqual(value, oldValue);
};

export const functionChangeDetection = <T>(value: T, oldValue: T): boolean => {
    if (!value || !oldValue) {
        return true;
    }
    if (typeof value === 'function' && typeof oldValue === 'function') {
        /**
         * Could probably return false and Assume static function, no need to re-render
         */
        return value.toString() !== oldValue.toString();
    }
    return dataChangeDetection(value, oldValue);
};
