import {
    BaseElement,
    booleanConverter,
    jsonConverter,
    property,
    PropertyValues,
    state,
} from '@umetrics/sartorius-ui-base';
import { Constructor } from '../data/types';
import { Domain, Domain2D, ScaleMap, ScaleZoomMap } from '../data';
import { defaultZoomLens } from '../data/constants';
import { WithContextInterface, WithContextProps } from '../data/plotContext';
import { Axis } from '../axis';
import { assertDefined, calculateNewDomain } from './zoomUtils';
import { mapEntries } from '../genericUtils';

export enum ZoomStrategy {
    Viewport = 'Viewport',
    MultiAxis = 'MultiAxis',
    RangeSelection = 'RangeSelection',
}

export const REGISTER_ZOOM_STRATEGY = 'register-zoom-strategy';

export interface ZoomAndPanProperties extends WithContextProps {
    domainBounds?: Domain2D;
    zoomStrategy?: ZoomStrategy;
    zoomExtensionSettings?: ZoomExtensionSettings;

    zoomLimitX?: number /* Minimum x zoom level. Could be for example minNumber / domain */;
    zoomLimitY?: number /* Minimum y zoom level. Could be for example minNumber / domain */;
    zoomSpeedX?: number;
    zoomSpeedY?: number;
    disableX?: boolean;
    disableY?: boolean;
    disableMainAreaXZoom?: boolean;
    disableMainAreaYZoom?: boolean;
    disableAxisX?: boolean;
    disableAxisY?: boolean;
    affectedScales?: string[];

    hideScrollbarX?: boolean /* Only for the ZoomAndPan component */;
    hideScrollbarY?: boolean /* Only for the ZoomAndPan component */;

    fitToView?: () => void;

    onZoom?: any;
    onPan?: any;
}

type ZoomPanEventDispatcher = <TDetail>(detail: TDetail) => void;

export declare abstract class ZoomBaseInterface extends BaseElement {
    zoomStrategy: ZoomStrategy;
    domainBounds?: Domain2D;
    zoomLimitX: number /* Minimum x zoom level. Could be for example minNumber / domain */;
    zoomLimitY: number /* Minimum y zoom level. Could be for example minNumber / domain */;
    zoomSpeedX: number;
    zoomSpeedY: number;
    disableX?: boolean;
    disableY?: boolean;
    disableMainAreaXZoom?: boolean;
    disableMainAreaYZoom?: boolean;
    disableAxisX?: boolean;
    disableAxisY?: boolean;
    affectedScales?: string[];
    customDomainBounds: Domain2D | undefined;

    boundsX: [number, number];
    boundsY: [number, number];

    _panState: PanState | null;
    _hoverAxis?: { orientation: 'vertical' | 'horizontal'; scaleKey: string };
    _cursorOverlay?: string;

    fitToView: () => void;
    /**
     * @docs private
     */
    _dispatchPanEvent: ZoomPanEventDispatcher;
    /**
     * @docs private
     */
    _dispatchZoomEvent: ZoomPanEventDispatcher;

    abstract plotRootElement?: HTMLElement;
    abstract scaleMap?: ScaleMap;
    abstract scaleZoomMap?: ScaleZoomMap;

    getDomainBounds(isVertical: boolean): [number, number];

    calculatePanLens({ offset, range, lens, bounds });

    panOnViewport(clientX: number, clientY: number, panState: PanState): ScaleZoomMap;

    mouseDownHandler(e: MouseEvent): void;

    wheelHandler(e: WheelEvent): void;
}

interface PanState {
    prevX: number;
    prevY: number;
    // scale's key that is affected in case of panning on the single axis
    scaleKey: string | null;
    slot: string;
}

interface ZoomPanEventDetail {
    viewbox?: Domain2D /* Deprecated, use zoomState and find x,y scales */;
    zoom?: {
        /* Deprecated, should not be needed. Or can be calculated on the outside with prev and new bounds */ x: number;
        y: number;
    };
    zoomState: ScaleZoomMap;
}

interface ZoomExtensionSettings {
    before?: number;
    after?: number;
    above?: number;
    below?: number;
}

const _defaultZoomExtensionSettings = {
    before: 0,
    after: 0,
    above: 0,
    below: 0,
} as ZoomExtensionSettings;

export const WithZoomBaseContext = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class ZoomBaseContextElement extends superClass implements ZoomAndPanProperties {
        // Static members

        // Private members

        // Protected members
        @property({ type: String, attribute: 'zoom-strategy' })
        zoomStrategy: ZoomStrategy = ZoomStrategy.Viewport; // Deprecated, should be MultiAxis default
        @property()
        zoomExtensionSettings?: ZoomExtensionSettings; // Non-used functionality. To be removed.

        @property()
        domainBounds: Domain2D | undefined; // Domain bound translated to boundsX and boundsY at willUpdate
        @property()
        customDomainBounds: Domain2D | undefined; // Yet another domain bound

        @property({ type: Number, reflect: false, attribute: 'zoom-limit-x' })
        zoomLimitX = 0.001;
        @property({ type: Number, reflect: false, attribute: 'zoom-limit-y' })
        zoomLimitY = 0.001;

        @property({ type: Number, reflect: false, attribute: 'zoom-speed-x' })
        zoomSpeedX = 0.001;
        @property({ type: Number, reflect: false, attribute: 'zoom-speed-y' })
        zoomSpeedY = 0.001;

        @property({ type: Boolean, attribute: 'disable-x', reflect: false, converter: booleanConverter })
        disableX?: boolean;
        @property({ type: Boolean, attribute: 'disable-y', reflect: false, converter: booleanConverter })
        disableY?: boolean;
        @property({ type: Boolean, attribute: 'disable-main-area-x-zoom', reflect: false, converter: booleanConverter })
        disableMainAreaXZoom?: boolean;
        @property({ type: Boolean, attribute: 'disable-main-area-y-zoom', reflect: false, converter: booleanConverter })
        disableMainAreaYZoom?: boolean;
        @property({ type: Boolean, attribute: 'disable-axis-x', reflect: false, converter: booleanConverter })
        disableAxisX?: boolean;
        @property({ type: Boolean, attribute: 'disable-axis-y', reflect: false, converter: booleanConverter })
        disableAxisY?: boolean;
        @property({
            type: Array,
            attribute: 'affected-scales',
            reflect: false,
            converter: jsonConverter,
            hasChanged(value?: string[], oldValue?: string[]) {
                return Boolean(value?.length !== oldValue?.length || value?.some((v, i) => v !== oldValue?.[i]));
            },
        })
        affectedScales?: string[];

        interactionTarget?: HTMLDivElement;
        interactionTargetSlot?: string;

        boundsX: [number, number] = defaultZoomLens;
        boundsY: [number, number] = defaultZoomLens;

        @state()
        _panState: PanState | null = null;
        @state()
        _hoverAxis?: { orientation: 'vertical' | 'horizontal'; scaleKey: string };
        @state()
        _cursorOverlay?: string;

        abstract plotRootElement?: HTMLElement;
        abstract scaleMap?: ScaleMap;
        abstract scaleZoomMap?: ScaleZoomMap;
        abstract fittedDomain?: Domain2D;
        abstract scaleX?: string;
        abstract scaleY?: string;

        constructor(...args: any[]) {
            super(args);

            this._axisMouseEnterHandler = this._axisMouseEnterHandler.bind(this);
            this._axisMouseLeaveHandler = this._axisMouseLeaveHandler.bind(this);
            this._handleKeydown = this._handleKeydown.bind(this);
        }

        public disconnectedCallback(): void {
            super.disconnectedCallback();

            this.registerZoomStrategy(undefined);
            this._cleanupZoomAndPan();
        }

        public fitToView() {
            this._resetZoomAndPanBounds();
        }

        _dispatchPanEvent<TDetail>(detail: TDetail): void {
            this.dispatchEvent(new CustomEvent('pan', { bubbles: true, detail }));
        }

        _dispatchZoomEvent<TDetail>(detail: TDetail): void {
            this.dispatchEvent(new CustomEvent('zoom', { bubbles: true, detail }));
        }

        getDomainBounds(isVertical: boolean): [number, number] {
            if (isVertical) {
                return this.customDomainBounds ? this.customDomainBounds.y : this.boundsY;
            }
            return this.customDomainBounds ? this.customDomainBounds.x : this.boundsX;
        }

        calculatePanLens = ({
            offset,
            range,
            lens,
            bounds,
        }: {
            offset: number;
            range: number[];
            lens: [number, number];
            bounds: [number, number];
        }): [number, number] => {
            const ratio = 1 / (range[1] - range[0]);

            const newLensMin = lens[0] - offset * ratio;
            const newLensMax = lens[1] - offset * ratio;

            return newLensMin > bounds[0] && newLensMax < bounds[1] ? [newLensMin, newLensMax] : lens;
        };

        public mouseDownHandler = (e: MouseEvent): void => {
            const zoomOriginEl: WithContextInterface | undefined = this.getZoomOriginElement(e);

            if (!zoomOriginEl) {
                return;
            }
            this._cursorOverlay = 'grabbing';
            // capture state at the moment of pan start
            this._panState = {
                prevX: e.clientX,
                prevY: e.clientY,
                scaleKey: zoomOriginEl.id?.startsWith('main-') ? null : (e.target as Axis).scaleKey,
                slot: zoomOriginEl.id,
            };

            window.addEventListener('mousemove', this.panMoveHandler);
            window.addEventListener('mouseup', this.panEndHandler);
        };

        public panOnViewport(
            clientX: number,
            clientY: number,
            panState: PanState = { prevY: 0, prevX: 0, scaleKey: null, slot: '' }
        ): ScaleZoomMap {
            assertDefined(this.scaleMap);

            const slot: string = this?.interactionTargetSlot || this.slot || 'main-area';
            if (panState.slot.startsWith('main-') && panState.slot !== slot) {
                assertDefined(this.scaleZoomMap);
                return this.scaleZoomMap;
            }

            return mapEntries(this.scaleMap, ([scaleKey, { scale, orientation }]) => {
                assertDefined(this.scaleZoomMap);
                const lens = this.scaleZoomMap[scaleKey];

                const isVertical = orientation === 'vertical';
                const isAffected = this.affectedScales === undefined || this.affectedScales.includes(scaleKey);
                if (!isAffected) {
                    return lens;
                }

                return this.calculatePanLens({
                    offset: isVertical ? clientY - panState.prevY : clientX - panState.prevX,
                    range: scale.range(),
                    lens: this.scaleZoomMap[scaleKey],
                    bounds: this.getDomainBounds(isVertical),
                });
            });
        }

        public wheelHandler = (e: WheelEvent): void => {
            const zoomOriginEl: WithContextInterface | undefined = this.getZoomOriginElement(e);
            if (!zoomOriginEl) {
                // For example wheel in legend, watermark...
                return;
            }
            e.preventDefault();
            // Get delta of zoom
            const { target, clientX, clientY, deltaX, deltaY } = e;
            const delta = -(deltaY + deltaX);
            this._doZoom(target, zoomOriginEl, delta, clientX, clientY);
        };

        protected firstUpdated(_props: PropertyValues<this>): void {
            super.firstUpdated(_props);
            this._setupZoomAndPan();
            this.registerZoomStrategy(this.zoomStrategy);
        }

        protected willUpdate(_changedProperties: PropertyValues<this>): void {
            super.willUpdate(_changedProperties);

            if (_changedProperties.has('domainBounds') || _changedProperties.has('fittedDomain')) {
                if (this.domainBounds) {
                    this.boundsX = this.domainBounds.x ?? defaultZoomLens;
                    this.boundsY = this.domainBounds.y ?? defaultZoomLens;
                }
            }
            if (_changedProperties.has('zoomStrategy')) {
                this.registerZoomStrategy(this.zoomStrategy);
            }
        }

        private _resetZoomAndPanBounds = () => {
            if (this.interactionTarget) {
                const rect = this.interactionTarget.getBoundingClientRect();
                const zoomExtension = this._zoomExtensionSettings();
                if (
                    zoomExtension.before !== 0 ||
                    zoomExtension.after !== 0 ||
                    zoomExtension.above !== 0 ||
                    zoomExtension.below !== 0
                ) {
                    const [xExtent, yExtent] = [this.boundsX[1] - this.boundsY[0], this.boundsY[1] - this.boundsY[0]];
                    const xMin = this.boundsX[0] - (zoomExtension.before! / rect.width) * xExtent;
                    const xMax = this.boundsX[1] + (zoomExtension.after! / rect.width) * xExtent;
                    const yMin = this.boundsY[0] - (zoomExtension.above! / rect.height) * yExtent;
                    const yMax = this.boundsY[1] + (zoomExtension.below! / rect.height) * yExtent;
                    this.boundsX = [xMin, xMax];
                    this.boundsY = [yMin, yMax];
                }
            }

            let oneReset = false;

            const updatedZoomState: ScaleZoomMap = !this.scaleMap
                ? {}
                : mapEntries(this.scaleMap, ([scaleKey, { orientation }]) => {
                      const lens = this.scaleZoomMap![scaleKey];
                      const org = this.getDomainBounds(orientation === 'vertical');
                      if (lens[0] !== org[0] && lens[1] !== org[1]) {
                          oneReset = true;
                      }
                      return org;
                  });

            if (oneReset) {
                this._dispatchPanEvent<ZoomPanEventDetail>({
                    zoomState: updatedZoomState,
                    viewbox: {
                        x: updatedZoomState[this.scaleX ?? 'zoomX'] ?? defaultZoomLens,
                        y: updatedZoomState[this.scaleY ?? 'zoomY'] ?? defaultZoomLens,
                    },
                });
            }
        };

        private _zoomExtensionSettings = () => {
            if (!this.zoomExtensionSettings) {
                return _defaultZoomExtensionSettings;
            }

            return Object.assign(_defaultZoomExtensionSettings, this.zoomExtensionSettings);
        };

        private registerZoomStrategy(zoomStrategy: ZoomStrategy | undefined): void {
            this.dispatchEvent(new CustomEvent(REGISTER_ZOOM_STRATEGY, { bubbles: true, detail: zoomStrategy }));
        }

        private _setupZoomAndPan() {
            if (!this.plotRootElement) {
                return;
            }

            this.plotRootElement.addEventListener('wheel', this.wheelHandler);
            this.plotRootElement.addEventListener('mousedown', this.mouseDownHandler);
            this.plotRootElement.addEventListener('enter', this._axisMouseEnterHandler);
            this.plotRootElement.addEventListener('leave', this._axisMouseLeaveHandler);

            document.addEventListener('keydown', this._handleKeydown);
        }

        private _cleanupZoomAndPan() {
            if (!this.plotRootElement) {
                return;
            }
            this.plotRootElement.removeEventListener('wheel', this.wheelHandler);
            this.plotRootElement.removeEventListener('mousedown', this.mouseDownHandler);
            this.plotRootElement.removeEventListener('enter', this._axisMouseEnterHandler);
            this.plotRootElement.removeEventListener('leave', this._axisMouseLeaveHandler);
            document.removeEventListener('keydown', this._handleKeydown);
        }

        private _axisMouseEnterHandler(e) {
            const { scaleKey, orientation } = e.detail;
            if (this._panState) {
                return;
            }

            this._hoverAxis = { scaleKey, orientation };

            if (orientation === 'vertical' && !this.disableY && !this.disableAxisY) {
                e.target.style.setProperty('cursor', 'ns-resize');
            } else if (orientation === 'horizontal' && !this.disableX && !this.disableAxisX) {
                e.target.style.setProperty('cursor', 'ew-resize');
            }
        }

        private _axisMouseLeaveHandler(e) {
            this._hoverAxis = undefined;
            e.target.style.removeProperty('cursor');
        }

        private panEndHandler = (): void => {
            this._panState = null;
            this._cursorOverlay = undefined;
            window.removeEventListener('mousemove', this.panMoveHandler);
            window.removeEventListener('mouseup', this.panEndHandler);
        };

        private panMoveHandler = (e: MouseEvent): void => {
            if (!this._panState || !this.scaleMap || !this.scaleZoomMap) {
                return;
            }

            // abort panning in case when single axis was captured (on `mousedown`) but in the middle `scaleSettings` has changed
            // and new created scaleMap does not contain it
            if (this._panState.scaleKey) {
                const scale = this.scaleMap[this._panState.scaleKey];
                const disabledAxis = (axisState, scaleOrientation) =>
                    axisState && scale.orientation === scaleOrientation;
                if (
                    !scale ||
                    disabledAxis(this.disableAxisX, 'horizontal') ||
                    disabledAxis(this.disableAxisY, 'vertical')
                ) {
                    this.panEndHandler();
                    return;
                }
            }

            const { clientX, clientY } = e;

            // CefSharp client (https://cefsharp.github.io/) specific
            // The 'mousemove' event gets triggered right after 'mousedown'
            // that cause an excessive 'pan' event with the same cursor position
            if (clientX === this._panState.prevX && clientY === this._panState.prevY) {
                return;
            }

            const updatedZoomState = this._panState.scaleKey
                ? this.panOnSingleAxis(clientX, clientY)
                : this.panOnViewport(clientX, clientY, this._panState);

            this._panState = {
                ...this._panState,
                prevX: clientX,
                prevY: clientY,
            };

            const getUpdateZoomState = (scaleProp, defaultName) =>
                updatedZoomState[scaleProp ?? defaultName] ?? defaultZoomLens;

            this._dispatchPanEvent<ZoomPanEventDetail>({
                zoomState: updatedZoomState,
                viewbox: {
                    x: getUpdateZoomState(this.scaleX, 'zoomX'),
                    y: getUpdateZoomState(this.scaleY, 'zoomY'),
                },
            });
        };

        private panOnSingleAxis(clientX: number, clientY: number): ScaleZoomMap {
            // check was made in outer scope
            assertDefined(this.scaleMap);
            assertDefined(this._panState?.scaleKey);
            assertDefined(this.scaleZoomMap);

            const { scale, orientation } = this.scaleMap[this._panState.scaleKey];
            const isVertical = orientation === 'vertical';

            let mappedKey;
            if (this.zoomStrategy === 'Viewport') {
                if (isVertical) {
                    mappedKey = this.scaleY;
                } else {
                    mappedKey = this.scaleX;
                }
            } else {
                mappedKey = this._panState.scaleKey;
            }

            return {
                ...this.scaleZoomMap,
                [mappedKey]: this.calculatePanLens({
                    offset: isVertical ? clientY - this._panState.prevY : clientX - this._panState.prevX,
                    range: scale.range(),
                    lens: this.scaleZoomMap[this._panState.scaleKey],
                    bounds: this.getDomainBounds(isVertical),
                }),
            };
        }

        private getZoomOriginElement(e: MouseEvent): WithContextInterface | undefined {
            return e
                .composedPath()
                .find(
                    (el: EventTarget) =>
                        (el as HTMLElement).id?.startsWith('main-') || (el as HTMLElement).id?.includes('axis')
                ) as WithContextInterface | undefined;
        }

        private _doZoom(target, zoomOriginEl, delta, clientX, clientY) {
            if (!this.scaleZoomMap || !this.scaleMap || !zoomOriginEl) {
                return;
            }

            let updatedZoomState: ScaleZoomMap;
            const slot: string = this?.interactionTargetSlot || this.slot || 'main-area';
            if (zoomOriginEl.id.startsWith('main-') && zoomOriginEl.id !== slot) {
                return;
            }

            // handle zoom on main area that affects all visualization entities at once
            if (zoomOriginEl === this || zoomOriginEl.id === slot) {
                updatedZoomState = this._doZoomOnMain(this.scaleMap, zoomOriginEl, clientX, clientY, delta);
            } else {
                const axisComponent: Axis = target as Axis;
                if (
                    (axisComponent.isVertical && this.disableAxisY) ||
                    (!axisComponent.isVertical && this.disableAxisX)
                ) {
                    return;
                }
                updatedZoomState = this._doZooomOnAxis(this.scaleZoomMap, axisComponent, delta, clientX, clientY);
            }

            this._dispatchZoomEvent<ZoomPanEventDetail>({
                zoomState: updatedZoomState,
                viewbox: {
                    x: updatedZoomState[this.scaleX ?? 'zoomX'] ?? defaultZoomLens,
                    y: updatedZoomState[this.scaleY ?? 'zoomY'] ?? defaultZoomLens,
                },
            });
        }

        private _doZoomOnMain(scaleMap: ScaleMap, zoomOriginEl, clientX, clientY, delta) {
            const mainAreaRect = zoomOriginEl.getBoundingClientRect();

            return mapEntries(scaleMap, ([scaleKey, { scale, orientation }]) => {
                const lens = this.scaleZoomMap![scaleKey];
                const isScaleVertical = orientation === 'vertical';
                const isYDisabled = isScaleVertical && (this.disableY || this.disableMainAreaYZoom);
                const isXDisabled = !isScaleVertical && (this.disableX || this.disableMainAreaXZoom);
                const isAffected = this.affectedScales === undefined || this.affectedScales.includes(scaleKey);
                if (isYDisabled || isXDisabled || !isAffected) {
                    return lens;
                }

                const pivot = this.resolveZoomPivot({
                    clientX,
                    clientY,
                    isScaleVertical,
                    range: scale.range(),
                    lens,
                    domRect: mainAreaRect,
                });

                const zoomDelta = this.getZoomDelta(delta, isScaleVertical);
                const zoomLimit = this.getZoomLimit(isScaleVertical);
                const domainBounds = this.getDomainBounds(isScaleVertical);

                return calculateNewDomain(lens, pivot, zoomDelta, zoomLimit, domainBounds);
            });
        }

        private _doZooomOnAxis(scaleZoomMap, axisComponent: Axis, delta, clientX, clientY) {
            const scaleKey = axisComponent.scaleKey;
            const lens = scaleZoomMap[scaleKey];
            const isScaleVertical = axisComponent.isVertical;
            const zoomDelta = this.getZoomDelta(delta, isScaleVertical);
            const zoomLimit = this.getZoomLimit(isScaleVertical);
            const domainBounds = this.getDomainBounds(isScaleVertical);

            const axisRange: number[] | undefined = axisComponent.scale?.range();

            assertDefined(axisRange);

            const pivot = this.resolveZoomPivot({
                clientX,
                clientY,
                isScaleVertical,
                range: axisRange,
                lens,
                domRect: axisComponent.getBoundingClientRect(),
            });

            let mappedKey;
            if (this.zoomStrategy === 'Viewport') {
                if (isScaleVertical) {
                    mappedKey = this.scaleY;
                } else {
                    mappedKey = this.scaleX;
                }
            } else {
                mappedKey = scaleKey;
            }
            return {
                ...this.scaleZoomMap,
                [mappedKey]:
                    (isScaleVertical && (this.disableY || this.disableAxisY)) ||
                    (!isScaleVertical && (this.disableX || this.disableAxisY))
                        ? lens
                        : calculateNewDomain(lens, pivot, zoomDelta, zoomLimit, domainBounds),
            };
        }

        private getZoomDelta(delta: number, isVertical: boolean): number {
            return 1 + delta * ((isVertical ? this.zoomSpeedY : this.zoomSpeedX) ?? 0.001);
        }

        private getZoomLimit(isVertical: boolean): number {
            return (isVertical ? this.zoomLimitY : this.zoomLimitX) ?? 0.001;
        }

        private resolveZoomPivot = ({
            clientX,
            clientY,
            isScaleVertical,
            lens,
            range,
            domRect: { width, height, left, top },
        }: {
            clientX: number;
            clientY: number;
            isScaleVertical: boolean;
            lens: Domain;
            range: number[];
            domRect: DOMRect;
        }): number => {
            const screenX = clientX !== -1 ? clientX - left : width / 2;
            const screenY = clientY !== -1 ? clientY - top : height / 2;

            return isScaleVertical
                ? ((lens[1] - lens[0]) / height) * ((range[0] - range[1]) * lens[1] - screenY)
                : ((lens[1] - lens[0]) / width) * (screenX + (range[1] - range[0]) * lens[0]);
        };

        private _handleKeydown(e) {
            if ((e.ctrlKey || e.metaKey) && this.interactionTarget?.getAttribute('hover') !== null) {
                const zoomDelta = 10;
                const panDelta = 10;
                if (e.key === '+') {
                    this._doZoom(this, this, zoomDelta, -1, -1);
                    e.preventDefault();
                } else if (e.key === '-') {
                    this._doZoom(this, this, -zoomDelta, -1, -1);
                    e.preventDefault();
                } else if (e.key === '0') {
                    this._resetZoomAndPanBounds();
                    e.preventDefault();
                } else {
                    const panMap = {
                        ArrowUp: { prevX: 0, prevY: -panDelta },
                        ArrowDown: { prevX: 0, prevY: panDelta },
                        ArrowLeft: { prevX: panDelta, prevY: 0 },
                        ArrowRight: { prevX: -panDelta, prevY: 0 },
                    };

                    if (panMap[e.key]) {
                        e.clientX = 0;
                        e.clientY = 0;
                        this._panState = { ...panMap[e.key], scaleKey: null, slot: this.interactionTargetSlot };
                        this.panMoveHandler(e);
                        e.preventDefault();
                        this._panState = null;
                    }
                }
            }
        }
    }

    return ZoomBaseContextElement as Constructor<ZoomBaseInterface> & T;
};
